const TabHeader = (props: any) => {
  const { tabActive, setTabActive } = props;
  return <div className="tab-bar">
    <ul className="tab">
      <li data-status={tabActive === 'runing' ? 'is-active' : ''} onClick={() => setTabActive('runing')}>
        <div className="btn"><span>Mở</span> </div>
      </li>
      <li data-status={tabActive === 'done' ? 'is-active' : ''} onClick={() => setTabActive('done')}>
        <div className="btn"><span>Đã kết thúc</span> </div>
      </li>
      <li data-status={tabActive === 'result' ? 'is-active' : ''} onClick={() => setTabActive('result')}>
        <div className="btn"><span>Kết quả</span> </div>
      </li>
    </ul>
  </div>
}

export default TabHeader