/* eslint-disable jsx-a11y/anchor-is-valid */
import { useContext, useState } from "react";
import { useSelector, shallowEqual } from "react-redux";
import { AuthContext } from "../Auth/contexts/context";
import ChangePassword from "./changePassword";

const Account = (props: any) => {
  const [openChangePassword, setOpenChangePassword] = useState(false);
  const { authDispatch } = useContext(AuthContext);
  
  const { stateAuth } = useSelector(
    (state: any) => ({ stateAuth: state.auth.user }),
    shallowEqual
  );
  const handleLogout = () => {
    authDispatch({
      type: 'SIGN_OUT',
    });
    const nodeAuth = document.createAttribute("data-auth");
    nodeAuth.value = 'true';
    document.documentElement.attributes.setNamedItem(nodeAuth)
  }
  const [detail, setDetails] = useState(false);
  return <div id="overlayComponents">
    <div id="pagePanel" className="contactUsPanel" style={{ display: props.open ? 'block' : 'none' }} data-status="is-open">
      <div className="page page-setting has-app-bar" data-status="is-open" data-full="">
        <div className="page_header">
          <div className="main-bar">
            <div className="title">Tôi</div>
          </div>
        </div>
        <div id="divMe" className="page_content">
          <div className="c-me-setting">
            <div className="c-me-account">
              <div className="c-avatar"><i className="c-icon c-icon--avatar"></i></div>
              <div className="c-me-account__info">
                <span className="c-text-group" data-edit-nickname="true"><span
                  className="c-text-username">{stateAuth.username}</span>
                  <span className="c-text-nickname"></span>
                  <a
                    className="c-btn c-btn--icon"><i className="c-icon c-icon--edit"></i></a></span><span className="c-text">Loại tiền
                      tệ : UT </span></div><a className="c-btn c-btn--icon"><i className="c-icon c-icon--email"></i></a>
            </div>
            <div className="c-me-wallet c-me-wallet--credit" data-open={detail}>
              <div className="c-me-wallet__main">
                <div className="c-me-wallet__item"><span className="c-text-title">Số dư</span><span className="c-text-value">{stateAuth.profit ?? 0.00}</span>
                </div>
                <div className="c-me-wallet__item"><span className="c-text-title">Hạn mức còn lại</span><span
                  className="c-text-value">{stateAuth.wallet ?? 0.00}</span></div>
                <a className="c-btn c-btn--icon" onClick={() => setDetails(!detail)}>
                  <i className="c-icon c-icon--caret"></i>
                </a>
              </div>
              <div className="c-me-wallet__detail" >
                <div className="c-me-wallet__item"><span className="c-text-title">Chưa xử lý</span><span
                  className="c-text-value">{stateAuth.runing_amount_today ?? 0.00}</span></div>
                <div className="c-me-wallet__item"><span className="c-text-title">Giá trị ròng</span><span
                  className="c-text-value">0.00</span></div>
                <div className="c-me-wallet__item"><span className="c-text-title">Hạn mức được cấp</span><span
                  className="c-text-value">{stateAuth.credit_line}</span></div>
              </div>
            </div>
            <div className="c-me-bet-setting">
              <div className="c-me-bet-setting__header"><span className="c-text">Cài đặt cược</span></div>
              <div className="c-me-bet-setting__content">
                <div className="c-me-bet-setting__item"><a className="c-btn" data-active="false"><i
                  className="c-icon c-icon--me-bettype"></i></a><span className="c-text">Loại tỷ lệ cược</span></div>
                <div className="c-me-bet-setting__item"><a className="c-btn" data-active="false"><i
                  className="c-icon c-icon--me-stake"></i></a><span className="c-text">Tiền cược Mặc định</span></div>
                <div className="c-me-bet-setting__item"><a className="c-btn" data-active="false"><i
                  className="c-icon c-icon--me-quick-bet"></i></a><span className="c-text">Cược Nhanh</span></div>
                <div className="c-me-bet-setting__item"><a className="c-btn" data-active="true"><i
                  className="c-icon c-icon--me-better-odds"></i><span className="c-btn__tag">ON</span></a><span
                    className="c-text">Tỷ Lệ Cược Tốt</span></div>
                <div className="c-me-bet-setting__item"><a className="c-btn" data-active="false"><i
                  className="c-icon c-icon--me-sports-priority"></i></a><span className="c-text">Sắp xếp Trận đấu </span></div>
                <div className="c-me-bet-setting__item"><a className="c-btn" data-active="false"><i
                  className="c-icon c-icon--me-sports-list"></i></a><span className="c-text">Môn thể thao ưu tiên</span></div>
              </div>
            </div>
            <div className="c-me-list">
              <div className="c-me-list__item" onClick={() => setOpenChangePassword(!openChangePassword)}>
                <div className="c-me-list__label"><span className="c-text">Cài Đặt Mật Khẩu</span></div>
                <div className="c-me-list__value">
                  <div className="c-btn"><span className="c-text"></span><i className="c-icon c-icon--arrow-right"></i></div>
                </div>
              </div>
              <div className="c-me-list__item">
                <div className="c-me-list__label"><span className="c-text">Ngôn Ngữ</span></div>
                <div className="c-me-list__value">
                  <div className="c-btn"><span className="c-text">Tiếng Việt</span><i className="c-icon c-icon--arrow-right"></i></div>
                </div>
              </div>
              <div className="c-me-list__item">
                <div className="c-me-list__label"><span className="c-text">Liên hệ</span></div>
                <div className="c-me-list__value">
                  <div className="c-btn"><span className="c-text"></span><i className="c-icon c-icon--arrow-right"></i></div>
                </div>
              </div>
              <div className="c-me-list__item">
                <div className="c-me-list__label-vertical-top"><span className="c-text">Tin Tài khoản</span></div>
                <div className="c-me-list__value"><span className="c-text">Truy cập gần nhất : {stateAuth.last_time_login}</span><span
                  className="c-text">Giao dịch gần nhất : {stateAuth.last_time_bet}</span></div>
              </div>
              <div className="c-me-list__item">
                <div className="c-me-list__label c-w-100"><span className="c-text">Phiên bản</span></div>
                <div className="c-me-list__value c-w-100">
                  <div className="c-switch-version"><a className="c-btn" data-selected="true"><span className="c-text">Rút
                    gọn</span></a><a className="c-btn" data-selected="false"><span className="c-text">Tiêu chuẩn</span></a>
                  </div>
                </div>
              </div>
              <div className="c-me-list__item"><a className="c-btn" onClick={handleLogout}><span className="c-text">Thoát</span></a></div>
            </div>
          </div>
        </div>
      </div>
      <div id="subFunc">
        <ChangePassword openChangePassword={openChangePassword} setOpenChangePassword={setOpenChangePassword} />
      </div>
    </div>
  </div>
}

export default Account;