import SVG from "react-inlinesvg";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import iconHome from "../../Assets/images/appbar/icon-home.svg";
import iconSaba from "../../Assets/images/appbar/icon-saba.svg";
import iconLiveChat from "../../Assets/images/appbar/icon-live-chat.svg";
import iconSport from "../../Assets/images/appbar/icon-sports.svg";
import iconStatement from "../../Assets/images/appbar/icon-statement.svg";
import iconAccount from "../../Assets/images/appbar/icon-account.svg";
import Statement from "../Statement";
import Account from "../Account";

const AppBar = (props: any) => {
  const [selected, setSelected] = useState('sports')
  const [statement, setStatement] = useState(false)
  const [account, setAccount] = useState(false);
  const navigate = useNavigate();
  useEffect(() => {
    setStatement(false)
    setAccount(false)
    setSelected(props.selected);
  }, [props.selected])

  return <>
    {selected === 'statement' && <Statement open={statement} setSelected={setSelected}/>}
    {selected === 'account' && <Account open={account} />}
    <div className="appbar">
      <div className="appbar_item" >
        <div className="btn" onClick={() => navigate('/lobby')}><i className="iconsvg-home">
          <SVG src={iconHome} /></i>
          <div className="btn_text">Trang Chủ</div></div>
      </div>
      <div className="appbar_item" data-status="">
        <div className="btn"><i className="iconsvg-saba"><SVG src={iconSaba} /></i> <div className="btn_text">Tỷ số Saba</div></div>
      </div>
      <div className="appbar_item" data-status={selected === 'contactus' ? 'is-active' : ''} onClick={() => setSelected('contactus')}>
        <div className="btn"><i className="iconsvg-live-chat"><SVG src={iconLiveChat} /></i> <div className="btn_text">Hỗ trợ</div></div>
      </div>
      <div className="appbar_item" data-status={selected === 'sports' ? 'is-active' : ''} onClick={() => {
        if (props.navigate) {
          navigate('/sports');
        }
        setSelected('sports');
        setStatement(false)
        setAccount(false)
      }}>
        <div className="btn"><i className="iconsvg-sports"><SVG src={iconSport} /></i> <div className="btn_text">Thể thao</div></div>
      </div>
      <div className="appbar_item" data-status={selected === 'statement' ? 'is-active' : ''} onClick={() => { setSelected('statement'); setStatement(true) ; setAccount(false)}}>
        <div className="btn"><i className="iconsvg-statement"><SVG src={iconStatement} /></i><div className="c-badge-group"></div> <div className="btn_text">Sao kê</div></div>
      </div>
      <div className="appbar_item" data-status={selected === 'account' ? 'is-active' : ''} onClick={() => {
        setSelected('account')
        setAccount(true)
        setStatement(false)
      }}>
        <div className="btn"><i className="iconsvg-account"><SVG src={iconAccount} /></i> <div className="btn_text">Tôi</div></div>
      </div>
    </div>
  </>
}

export default AppBar;