import axios from "axios";

export function getStatementDetails() {
  // Authorization head should be fulfilled in interceptor.
  return axios.get(process.env.REACT_APP_API_URL + "/league/satementDetails?type=runing");
}

export function getStatementDetailsDone(date: any) {
  // Authorization head should be fulfilled in interceptor.
  return axios.get(process.env.REACT_APP_API_URL + "/league/satementDetails?date=" + date + "&type=done");
}

export function getResults(date: any) {
  return axios.get(process.env.REACT_APP_API_URL + "/league/result?day=" + date);
}

export function getStatement(date: any) {
  return axios.get(process.env.REACT_APP_API_URL + "/league/satement?day=" + date);
}
export function satementReport() {
  return axios.get(process.env.REACT_APP_API_URL + "/league/satementReport");
}
