/* eslint-disable jsx-a11y/anchor-is-valid */
import { useState } from "react";

const SportsHeader = (props: any) => {
  const { parlay, setParlay, gameType, setGameType, intervalEvent } = props;
  const [openMenu, setOpentMenu] = useState(false);
  const [filterActive, setFilterActive] = useState('live');

  const navHeader = () => {
    return <div className="header-dropdown" data-open={openMenu ? 'true' : 'false'}>
      <div className="header-dropdown__btn" onClick={() => setOpentMenu(!openMenu)}><span className="text">Thể Thao</span><i className="icon icon-arrow-bottom"></i></div>
      <div className="header-dropdown__container">
        <div className="header-dropdown__list">
          <div className="header-dropdown__item" data-selected="true">
            <div className="btn"><span className="text">Thể Thao</span></div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Cược xiên</span></div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Thể Thao Điện Tử</span></div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Thể Thao Ảo</span></div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Number Game</span></div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Cúp Thế Giới</span></div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Sòng bạc</span></div>
          </div>
          <div className="header-dropdown__item" data-open="false">
            <div className="btn"><span className="text">Casino Trực tuyến</span></div>
            <div className="header-dropdown__sub-list">
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image">
                    <img src="/Content/SiteSkin/_global/common/images/LiveCasino/allbet.png?v=202202172324" alt="Allbet" />
                  </div><span className="text">Allbet</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/aesexy.png?v=202202172324" alt="AE Sexy" />
                  </div><span className="text">AE Sexy</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/pplc.png?v=202202172324"
                    alt="PP Live Casino" /></div><span className="text">PP Live Casino</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/wm.png?v=202202172324" alt="WM" /></div>
                  <span className="text">WM</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/sagaming.png?v=202202172324"
                    alt="SA Gaming" /></div><span className="text">SA Gaming</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/bg.png?v=202202172324" alt="BG" /></div>
                  <span className="text">BG</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/bbin.png?v=202202172324" alt="BBIN" /></div>
                  <span className="text">BBIN</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/LiveCasino/ION.png?v=202202172324" alt="ION" /></div>
                  <span className="text">ION</span>
                </div>
              </div>
            </div>
          </div>
          <div className="header-dropdown__item" data-selected="false">
            <div className="btn"><span className="text">Cổng game Saba</span></div>
          </div>
          <div className="header-dropdown__item" data-open="false">
            <div className="btn"><span className="text">Keno/Xổ số</span></div>
            <div className="header-dropdown__sub-list">
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/rngkeno.png?v=202202172324" alt="RNG Keno" />
                  </div><span className="text">RNG Keno</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/maxgame.png?v=202202172324" alt="MaxGame" />
                  </div><span className="text">MaxGame</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/lottery.png?v=202202172324" alt="Xổ số" />
                  </div><span className="text">Xổ số</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/tablegame.png?v=202202172324"
                    alt="Trò chơi trên bàn" /></div><span className="text">Trò chơi trên bàn</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/lotterworld.png?v=202202172324"
                    alt="Lottoworld" /></div><span className="text">Lottoworld</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/lotto.png?v=202202172324" alt="Lô Đề" /></div>
                  <span className="text">Lô Đề</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/soccerlottery.png?v=202202172324"
                    alt="Xổ số Bóng đá" /></div><span className="text">Xổ số Bóng đá</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/nba-lottery.png?v=202202172324"
                    alt="Xổ Số NBA" /></div><span className="text">Xổ Số NBA</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Lottery/rngwar.png?v=202202172324" alt="Funky Games" />
                  </div><span className="text">Funky Games</span>
                </div>
              </div>
              <div className="header-dropdown__sub-item">
                <div className="btn">
                  <div className="header-dropdown__image"><img
                    src="/Content/SiteSkin/_global/common/images/Sports/normal/sport-169-2.png"
                    alt="Xổ Số Thể Thao Điện Tử" /></div><span className="text">Xổ Số Thể Thao Điện Tử</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  }

  const switchParlay = () => {
    return <div className="c-switch-to-parlay" data-selected={parlay ? 'true' : 'false'}>
      <a className="c-btn" onClick={() => setParlay(!parlay)}>
        <span className="c-text">Cược xiên</span>
        <i className="c-icon c-icon--successful"></i>
      </a>
      <div className="c-tooltip c-tooltip--bottom-right c-tooltip--highlight" data-open="false"><span className="c-text">Chuyển
        sang Cược Xiên</span></div>
    </div>
  }

  const headerFilter = () => {
    return <div className="header-filter" data-early="false">
      <div className="btn-bar bar-scroll">
        <div className="btn-group">
          <div className="btn" data-status={filterActive === 'live' ? 'is-active' : ''} onClick={() => {
            setFilterActive('live');
            props.setTab('live');
          }}><i className="c-icon c-icon--live"></i>
            <div className="btn_text"><span className="c-text">Trực tiếp</span><span className="c-badge c-badge--default">{props.totalLive}</span>
            </div>
          </div>
          <div className="btn" data-status={filterActive === 'today' ? 'is-active' : ''} onClick={() => {
            setFilterActive('today')
            props.setTab('today');
          }}>
            <div className="btn_text"><span className="c-text">Hôm nay</span><span className="c-badge c-badge--default">{props.totalToday + props.totalLive}</span>
            </div>
          </div>
          {gameType === 'socer' && <>
            <div id="EarlyDropdownTab" className="btn btn-dropdown" data-open="false" data-status="">
              <div className="btn_text"><span className="c-text">Sớm</span><span className="c-badge c-badge--default">920</span></div>
            </div>
            <div className="btn" data-status="">
              <div className="btn_text"><span className="c-text">Cược thắng</span><span className="c-badge c-badge--default">44</span>
              </div>
            </div>
          </>}
        </div>
      </div>
      <div className="dropdown dropdown-filter ">
        <div className="btn"><i className="icon icon-filter"></i></div>
        <ul className="dropdown-menu" role="menu">
          <li>
            <div className="checkbox"><input type="checkbox" className="icon icon-checkbox" id="option_3" /><label className="title"
            ><span className="c-text">Yêu Thích</span></label></div>
          </li>
          <li>
            <div className="checkbox"><input type="checkbox" className="icon icon-checkbox" id="option_1" /><label className="title"
            ><span className="c-text">Truyền Hình Trực Tiếp</span></label></div>
          </li>
          <li>
            <div className="checkbox"><input type="checkbox" className="icon icon-checkbox" id="option_2" /><label className="title"
            ><span className="c-text">Kèo Rung</span></label></div>
          </li>
          <li className="divider"></li>
          <li>
            <div className="btn">
              <div className="text">Chọn Giải Đấu(13/13)</div><i className="icon icon-arrow-right"></i>
            </div>
          </li>
        </ul>
      </div>
    </div>
  }

  return <div className="header" data-status="" data-gof="">
    <div className="header_status">
      <div className="text-balance">Yen 0.00</div>
    </div>
    <div className="header-menu">
      <div className="btn" data-open-sidebar="true" onClick={() => props.setOpentMenu(!props.openMenu)}><i className="icon icon-menu"></i></div>
      {navHeader()}
      {gameType === 'socer' && switchParlay()}
      <div className="btn btn-search"><i className="icon icon-search"></i></div>
    </div>
    <div className="header-submenu" data-status="is-open" data-arrow="true">
      <div className="submenu" data-status="is-open">
        <div className="btn" data-selected={gameType === 'socer' ? true : false} onClick={() => {
          clearInterval(intervalEvent);
          setGameType('socer');
        }}>
          <i className="icon icon-sport1">
            <i className="c-icon c-icon--live"></i>
          </i>Bóng đá
        </div>
        <div className="btn" data-selected={gameType === 'saba' ? true : false} onClick={() => {
          clearInterval(intervalEvent);
          setGameType('saba');
        }}>
          <i className="icon icon-sport997">
            <i className="c-icon c-icon--live"></i></i>
          Bóng Đá Saba</div>
        <div className="btn btn-esports" data-selected="false"><i className="icon icon-sport43"><i
          className="c-icon c-icon--live"></i></i>Thể Thao Điện Tử</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport2"><i className="c-icon c-icon--live"></i></i>Bóng rổ
        </div>
        <div className="btn" data-selected="false"><i className="icon icon-sport4"></i>Khúc côn cầu trên băng</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport5"><i className="c-icon c-icon--live"></i></i>Quần vợt
        </div>
        <div className="btn" data-selected="false"><i className="icon icon-sport6"><i className="c-icon c-icon--live"></i></i>Bóng
          chuyền</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport7"></i>Snooker/Pool</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport11"></i>Thể Thao Môtô</div>
        <div className="btn btn-cricket" data-selected="false"><i className="icon icon-sport50"><i
          className="c-icon c-icon--live"></i></i>Cricket</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport47"></i>Kabaddi</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport16"></i>Quyền anh</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport26"></i>Bóng bầu dục</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport18"><i className="c-icon c-icon--live"></i></i>Bóng
          bàn</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport24"></i>Bóng ném</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport31"></i>Giải Trí</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport55"></i>Tài chính</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport56"></i>Lô tô</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport175"></i>Bitcoin</div>
        <div className="btn" data-selected="false"><i className="icon icon-sport999"></i>Cược thắng</div>
      </div>
      <div className="header-child-menu" data-status=""></div>
      <div id="TFortune" className="gof__entrance-btn" data-gtm-vis-polling-id-13471504_180="612"
        data-gtm-vis-polling-id-13471504_181="613"></div>
    </div>
    {headerFilter()}
    <div className="dropdown dropdown-early ">
      <ul className="dropdown-menu">
        <li className=""><a><span className="c-text">Sớm</span><span className="c-badge c-badge--default">920</span></a></li>
        <li className=""><a><span className="c-text">Thg2 22 </span><span className="c-badge c-badge--default">66</span></a></li>
        <li className=""><a><span className="c-text">Thg2 23 </span><span className="c-badge c-badge--default">41</span></a></li>
        <li className=""><a><span className="c-text">Thg2 24 </span><span className="c-badge c-badge--default">36</span></a></li>
        <li className=""><a><span className="c-text">Thg2 25 </span><span className="c-badge c-badge--default">75</span></a></li>
        <li className=""><a><span className="c-text">Thg2 26 </span><span className="c-badge c-badge--default">229</span></a></li>
        <li className=""><a><span className="c-text">Thg2 27 </span><span className="c-badge c-badge--default">153</span></a></li>
        <li className=""><a><span className="c-text">Thg2 28 </span><span className="c-badge c-badge--default">24</span></a></li>
        <li className=""><a><span className="c-text">Thg3 01 </span><span className="c-badge c-badge--default">26</span></a></li>
        <li className=""><a><span className="c-text">Thg3 02 </span><span className="c-badge c-badge--default">43</span></a></li>
        <li className=""><a><span className="c-text">Thg3 03 </span><span className="c-badge c-badge--default">10</span></a></li>
        <li className=""><a><span className="c-text">Thg3 04 </span><span className="c-badge c-badge--default">31</span></a></li>
        <li className=""><a><span className="c-text">Thg3 05 </span><span className="c-badge c-badge--default">81</span></a></li>
        <li className=""><a><span className="c-text">Thg3 06 </span><span className="c-badge c-badge--default">73</span></a></li>
        <li className=""><a><span className="c-text">Thg3 07 </span><span className="c-badge c-badge--default">15</span></a></li>
        <li className=""><a><span className="c-text">Thg3 08 </span><span className="c-badge c-badge--default">3</span></a></li>
        <li className=""><a><span className="c-text">Thg3 09 </span><span className="c-badge c-badge--default">3</span></a></li>
        <li className=""><a><span className="c-text">Thg3 10 </span><span className="c-badge c-badge--default">3</span></a></li>
        <li className=""><a><span className="c-text">Thg3 24 </span><span className="c-badge c-badge--default">8</span></a></li>
      </ul>
    </div>
  </div>
}
export default SportsHeader;