import '../../Assets/css/loading-page.css';
import iconLoading1 from '../../Assets/images/loading/1.svg';
import iconLoading2 from '../../Assets/images/loading/2.svg';
import iconLoading3 from '../../Assets/images/loading/3.svg';
import iconLoading4 from '../../Assets/images/loading/4.svg';
import iconLoading5 from '../../Assets/images/loading/5.svg';
import iconLoading6 from '../../Assets/images/loading/6.svg';
import iconLoading7 from '../../Assets/images/loading/7.svg';
import iconLoading8 from '../../Assets/images/loading/8.svg';
import iconLoading9 from '../../Assets/images/loading/9.svg';
import iconLoading10 from '../../Assets/images/loading/10.svg';
import { useEffect, useState } from 'react';
import SVG from "react-inlinesvg";

const Loading = (props: any) => {
  const svgIcons = [
    {
      'icon': iconLoading1,
      'status': 'default'
    },
    {
      'icon': iconLoading2,
      'status': 'default'
    },
    {
      'icon': iconLoading3,
      'status': 'default'
    },
    {
      'icon': iconLoading4,
      'status': 'default'
    },
    {
      'icon': iconLoading5,
      'status': 'default'
    },
    {
      'icon': iconLoading6,
      'status': 'default'
    },
    {
      'icon': iconLoading7,
      'status': 'default'
    },
    {
      'icon': iconLoading8,
      'status': 'default'
    },
    {
      'icon': iconLoading9,
      'status': 'default'
    },
    {
      'icon': iconLoading10,
      'status': 'default'
    },
  ]

  const [icons, setIcons] = useState(svgIcons);
  const [percents, setPercents] = useState(0);
  const aroundIcon = () => {
    return <div className="l-gc-loading-page-icons">
      {icons.map((icon, idx) => {
        return <div key={idx} className={`l-gc-loading-page-icon l-gc-loading-page-icon-${idx + 1}`} data-status={icon.status}>
          <SVG src={icon.icon} />
        </div>
      })}
    </div>
  }

  useEffect(() => {
    icons[0].status = 'success';
    setIcons(icons);
    if (percents < 100) {
      if (percents >= 65) {
        if (props.done) {
          if (percents > 60) {
            icons[6].status = 'success';
            setIcons(icons);
          }
          if (percents > 70) {
            icons[7].status = 'success';
            setIcons(icons);
          }
          if (percents > 80) {
            icons[8].status = 'success';
            setIcons(icons);
          }
          if (percents > 90) {
            icons[9].status = 'success';
            setIcons(icons);
          }
          setTimeout(() => {
            setPercents(percents + 5);
          }, 100)
        }
      } else {
        if (percents > 10) {
          icons[1].status = 'success';
          setIcons(icons);
        }

        if (percents > 20) {
          icons[2].status = 'success';
          setIcons(icons);
        }

        if (percents > 30) {
          icons[3].status = 'success';
          setIcons(icons);
        }

        if (percents > 40) {
          icons[4].status = 'success';
          setIcons(icons);
        }

        if (percents > 50) {
          icons[5].status = 'success';
          setIcons(icons);
        }

        setTimeout(() => {
          setPercents(percents + 5);
        }, 100)
      }
    }

  }, [percents, icons, props.done])


  return <div className="l-gc-loading-page" style={{display: percents === 100 ? 'none' : ''}}>
    <div className="l-gc-loading-page__main">
      <div className="l-gc-loading-page-rings">
        <div className="l-gc-loading-page-ring-1">
          <div className="l-gc-loading-page-ring-2">
            <div className="l-gc-loading-page-ring-3">
              <span className="l-gc-loading-page-counter">{percents}%</span>
            </div>
          </div>
        </div>
      </div>
      {aroundIcon()}
    </div>
  </div>
}

export default Loading;