import { useEffect, useState } from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { actions } from "../App/Redux";
import { getStorage } from "../Helpers/LocalStorageHelpers";

const Header = () => {
  const { stateAuth } = useSelector(
    (state: any) => ({ stateAuth: state.auth.user }),
    shallowEqual
  );
  const dispatch = useDispatch();
  const [themeDark, setThemeDark] = useState(getStorage('theme') ? getStorage('theme') === 'dark' ? true : false : false);
  const handleChangeTheme = () => {
    dispatch(actions.setThemeDark(!themeDark));
    setThemeDark(!themeDark);
  }
  return <div className="c-header">
    <div className="c-header__logo"><span className="c-logo"></span></div>
    <div className="c-header__account">
      <div className="c-account-info"><span className="c-text">Chào Mừng</span><span className="c-text-username">
        {stateAuth.username}</span></div>
    </div>
    <div className="c-header__darkmode" onClick={handleChangeTheme}>
      <label className="c-switch-darkmode">
        <span className="c-switch-darkmode__btn"></span>
      </label>
    </div>
  </div>;
}

export default Header;