import { useEffect, useState } from "react"
import moment from "moment-timezone";
/* eslint-disable jsx-a11y/anchor-is-valid */
const Event = ({ event }: any) => {
  const [expanded, setExpanded] = useState(false)
  const [ss, setSS] = useState({
    h: 0,
    a: 0
  })

  useEffect(() => {
    let _ss = event.ss.split('-');
    setSS({
      h: _ss[0],
      a: _ss[1]
    })
  }, [event])
  return <li>
    <div className="heading"><a className="toggle collapsed" aria-expanded={expanded}>
      <div className="time-group">
        <div className=" time">{moment(event.start_date).format('DD/MM/YYYY h:m A')}</div>
        <div className="status">{event.time_status_name}</div>
      </div>
      <div className="team-group"><span className="team-name">{event.home}</span> <span className="score">{ss.h}</span></div>
      <div className="team-group"><span className="team-name">{event.away}</span> <span className="score">{ss.a}</span></div>
      <i className="icon icon-arrow-bottom" onClick={() =>setExpanded(!expanded)}></i>
    </a></div>
    <div className={`collapse ${expanded ? 'in': ''}`}>
      <div className="table-responsive">
        <table className="table table-striped table-condensed">
          <thead>
            <tr>
              <th>Tỷ Số Nửa Trận</th>
              <th>Kết Quả Cuối Cùng</th>
              <th>Trạng thái</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{event.hf_ss}</td>
              <td>{event.ss}</td>
              <td>
                <div className="status">Đang diễn ra</div><a className="btn detailed" aria-expanded="false"><span>Chi
                  tiết</span><i className="icon icon-arrow-bottom dark"></i></a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div className="more-result-detail collapse" aria-expanded="false"></div>
    </div>
  </li>
}

export default Event