import moment from "moment";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import BetDetail from "./BetDetail";

const BetRunning = (props: any) => {
  const navigate = useNavigate()
  const [loadingFold, setLoadingFold] = useState(false);
  const [foldOpen, setfoldOpen] = useState(true);
  const { bets, loading, setSelected } = props;
  useEffect(() => {
    if (foldOpen) {
      setLoadingFold(true)
      setTimeout(() => { setLoadingFold(false) }, 500)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [foldOpen])
  const perLoader = () => {
    return <div className="preloader preloader-min">
      <div className="preloader_spiner">
        <div className="preloader_circles">
          <div className="preloader_circle-1"></div>
          <div className="preloader_circle-2"></div>
        </div>
      </div>
    </div>
  }
  return <>
    <div className="c-page-section">
      {loading && perLoader()}
      {!loading && bets.data.bets.length === 0 && <div className="empty empty-noMatch">
        <div className="empty_container">
          <div className="empty_content">
            <div className="empty_pic"></div>
            <div className="empty_title">Không có Cược nào đang mở</div>
            <div className="empty_text">
              <p>Nhấn vào nút bên dưới để quay trở về trang trước và bắt đầu cá cược.</p>
              <div className="empty_btn" onClick={() => setSelected('sports')}>
                <div id="empty_back" className="btn btn-highlight" >
                  <div className="btn_text" >Quay lại</div>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>}
      {!loading && bets.data.bets.length > 0 && <div className="fold" data-status={foldOpen ? 'is-open' : ''}>
        <div className="fold_header">
          <div className="fold_item touch">
            <div className="text-time">
              {moment(bets.data.betsInfo?.date).add(1, 'h').format('YYYY/MM/DD').toString() ?? ''} <span className="text-amount"> ({bets.data.betsInfo?.count ?? 0}) </span>
              <i className="icon icon-arrow-top" onClick={() => setfoldOpen(!foldOpen)}></i>
            </div>
            <div className="info">
              <div className="info_title">Tiền cược:</div>
              <div className="info_value">{bets.data.betsInfo?.stake ?? 0}</div>
            </div>
          </div>
        </div>
        <div className="fold_container">
          {loadingFold && <div className="preloader preloader-min">
            <div className="preloader_spiner">
              <div className="preloader_circles">
                <div className="preloader_circle-1"></div>
                <div className="preloader_circle-2"></div>
              </div>
            </div>
          </div>}
          {(foldOpen && !loadingFold) && bets.data.bets.map((bet: any, idx: number) => {
            return <BetDetail bet={bet} key={idx} setSelected={setSelected}/>
          })}
        </div>
      </div>}
    </div>
  </>
}
export default BetRunning