import { useContext, useState } from "react";
import { Link, useNavigate, useLocation } from "react-router-dom";
import { useFormik } from "formik";
import * as Yup from "yup";
import { AuthContext } from "../contexts/context";
import { login } from "../_redux/authCrud";
import { useDispatch } from "react-redux";
import { actions } from "../_redux/authRedux";

const initialValues = {
  username: "",
  password: "",
};

const Login = (props: any) => {
  const [loading, setLoading] = useState(false);
  const LoginSchema = Yup.object().shape({
    username: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required('Tên truy cập không đúng'),
    password: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required('Mật khẩu không đúng'),
  });
  const { authDispatch } = useContext(AuthContext);
  const navigate = useNavigate()
  let location = useLocation();
  const dispatch = useDispatch();

  let { from }: any = location.state || { from: { pathname: "/" } };

  const enableLoading = () => {
    setLoading(true);
  };

  const disableLoading = () => {
    setLoading(false);
  };

  const formik: any = useFormik({
    initialValues,
    validationSchema: LoginSchema,
    onSubmit: (values, { setStatus, setSubmitting }) => {
      enableLoading();
      setTimeout(() => {
        login(values.username, values.password)
          .then(({ data }: any) => {
            disableLoading();
            if (data.code === 'SUCCESS') {
              authDispatch({
                type: 'SIGNIN_SUCCESS',
                payload: data.token
              });
              dispatch(actions.login(data.token))
              navigate(from)
            } else {
              alert(data.message)
            }
          })
          .catch(() => {
            disableLoading();
            setSubmitting(false);
            setStatus('Tên hoặc mật khẩu không hộ lệ');
          });
      }, 1000);
    },
  });
  return <div className={`login-block${props.show ? ' in' : ''}`}>
    <form onSubmit={formik.handleSubmit} className="loginPanel" style={{ display: 'block' }}>
      <div className="form-group">
        <div className="inputPanel">
          <div className="select-group form-control">
            <i className="icon icon-arrow-bottom dark"></i>
            <span id="Lang" className="value">Tiếng Việt</span>
            <select>
              <option value="vi-VN" id="vi-VN">Tiếng Việt</option>
              <option value="en-US" id="en-US">English</option>
              <option value="zh-TW" id="zh-TW">繁體中文</option>
            </select>
          </div>
          <input name="username" className="form-control" type="text" placeholder="Tên truy cập"  {...formik.getFieldProps("username")} />
          <input name="password" className="form-control" type="password" placeholder="Mật khẩu"  {...formik.getFieldProps("password")} />
          <div className="btn btn-cancel" role="button"><i className="icon icon-cancel"></i></div>
          <div id="GestureToggle" className="btn btn-gesture-password" role="button" style={{ display: 'none' }}>
            <i className="icon icon-gesture-password"></i>
          </div>
        </div>
        <div id="warringMessageFade" className="tooltip bottom fade tooltip-username" role="tooltip">
          <div className="tooltip-arrow"></div>
          <div id="warringMessage" className="tooltip-inner"></div>
        </div>
        <div className="box">
          <div className="checkbox rememberMe">
            <input type="checkbox" id="rememberMe" className="icon icon-checkbox" />
            <label>Ghi Nhớ</label>
          </div>
        </div>
        <button type="submit" disabled={formik.isSubmitting} className="btn btn-primary btn-block btn-login">Đăng nhập</button>
      </div>
    </form>
    <div className="loading" style={{ display: loading ? 'block' : 'none' }}>
      <i className="icon-loading"><span></span><span></span><span></span><span></span></i></div>
  </div>
}

export default Login;
