import React, { useReducer } from 'react';
import { getStorage, removeStorage, setStorage } from '../../Helpers/LocalStorageHelpers';
import { AuthContext } from './context';

function reducer(state: any, action: any) {
  switch (action.type) {
    case 'SIGNIN':
      return {
        ...state,
      };
    case 'SIGNIN_SUCCESS':
      setStorage('authorization', action.payload, null);
      return true;
    case 'SIGN_OUT':
      removeStorage('authorization');
      return false;
    default:
      return null;
  }
}

const AuthProvider = (props: any) => {
  const [authState, authDispatch] = useReducer(
    reducer,
    !!getStorage('authorization'),
  );
  
  return (
    <AuthContext.Provider
      value={{
        authState,
        authDispatch,
      }}
    >
      {props.children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;
