import { Key, useEffect, useState } from "react";
import { shallowEqual, useSelector } from "react-redux";
import SVG from "react-inlinesvg";

import iconSuccess from '../../Assets/images/success.svg';
import iconReject from '../../Assets/images/reject.svg';
import { createGroup, inPlayById } from "./sportCrud";

export const Parlay = (props: any) => {
  const { parlayData, setShowParlay, setParlayData, showParlay, live, data, setAppBarSelect } = props;
  const [betBalance, setBetBalance] = useState([] as string[]);
  const [betBalanceFormat, setBetBalanceFormat] = useState(0);
  const [focus, setFocus] = useState(false);
  const [msg, setMsg] = useState('');
  const [totalBet, setTotalBet] = useState(0);
  const [confirm, setConfirm] = useState(false);
  const [betParlay, setBetParlay] = useState([] as any);
  const [process, setProcess] = useState('process');
  const { stateApp } = useSelector(
    (state: any) => ({ stateApp: state }),
    shallowEqual
  );
  const updateData = (id: string) => {
    setTimeout(() => {
      inPlayById(id).then(({ data }) => {
        if (data.data.status === 'pending') {
          updateData(id);
        } else if (data.data.status === 'runing') {
          setProcess('accept');
        } else if (data.data.status === 'cancel') {
          setProcess('reject');
        }
      })
    }, 3000)
  }

  const handicapValue = (odd: any, betType: string) => {
    let betT = betType.split('.');
    let bet_position = -1;
    if (betT[1] === 'home_od' || betT[1] === 'over_od') {
      bet_position = 0
    }
    if (betT[1] === 'away_od' || betT[1] === 'under_od') {
      bet_position = 1
    }

    return ((odd.handicap_team === 'home' ? -1 : 1) * (bet_position === 0 ? 1 : -1)) * parseFloat(odd.handicap_value);
  }

  useEffect(() => {
    let result = 1;
    let _betParlay: any = [];
    parlayData.forEach((parlay: any) => {
      let league: any;
      league = live ? live.find((l: any) => l.league_id === parlay.leagueId) : null;
      !league && (league = data ? data.find((l: any) => l.league_id === parlay.leagueId) : null);
      if (league) {
        const event = league.events.find((e: any) => e.event_id === parlay.eventId);
        if (event) {
          let odd = event.odds.find((o: any) => o.odd_id === parlay.oddId);
          if (odd) {
            const betV = parlay.betType.split('.');
            let betValue = odd[betV[0]][betV[1]];
            result *= convertMalayToDecimal(parseFloat(betValue));
            _betParlay.push(
              {
                ...parlay,
                betValue: betValue
              }
            );
          }
        }
      }

    })
    setTotalBet(result)
    setBetParlay(_betParlay);
    setBetBalanceFormat(0)
    setProcess('process');
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [parlayData])
  const { user } = stateApp.auth;
  const comtickets = (parlay: any) => {

    let league: any;
    league = live ? live.find((l: any) => l.league_id === parlay.leagueId) : null;
    !league && (league = data ? data.find((l: any) => l.league_id === parlay.leagueId) : null);
    if (league) {
      const event = league.events.find((e: any) => e.event_id === parlay.eventId);
      if (event) {
        let odd = event.odds.find((o: any) => o.odd_id === parlay.oddId);
        if (odd) {
          let betChoice;
          let betType;
          let betGoal;
          const betV = parlay.betType.split('.');
          let betValue = odd[betV[0]][betV[1]];
          // setTotalBet(1);

          if (parlay.betType === 'ft_hdp.home_od' || parlay.betType === 'ft_hdp.away_od' || parlay.betType === 'hf_hdp.home_od' || parlay.betType === 'hf_hdp.away_od') {
            if (parlay.betType.lastIndexOf('ft_hdp', 0) === 0) {
              betType = 'Trực tiếp Cược Chấp';
            } else if (parlay.betType.lastIndexOf('hf_hdp', 0) === 0) {
              betType = 'Trực tiếp Cược Chấp / Hiệp 1';
            }
            if (parlay.betType === 'ft_hdp.home_od' || parlay.betType === 'hf_hdp.home_od') {
              betChoice = event.home
            }

            if (parlay.betType === 'ft_hdp.away_od' || parlay.betType === 'hf_hdp.away_od') {
              betChoice = event.away
            }
            if (betV[0] === 'ft_hdp') {
              betGoal = handicapValue(odd.ft_hdp, parlay.betType);
            }

            if (betV[0] === 'hf_hdp') {
              betGoal = handicapValue(odd.hf_hdp, parlay.betType);
            }

          }

          if (parlay.betType === 'ft_ou.over_od' || parlay.betType === 'ft_ou.under_od' || parlay.betType === 'hf_ou.over_od' || parlay.betType === 'hf_ou.under_od') {

            if (parlay.betType.lastIndexOf('ft_ou', 0) === 0) {
              betType = 'Trực tiếp Tài/Xỉu';
            } else if (parlay.betType.lastIndexOf('hf_ou', 0) === 0) {
              betType = 'Trực tiếp Tài/Xỉu / Hiệp 1';
            }
            if (parlay.betType === 'ft_ou.over_od' || parlay.betType === 'hf_ou.over_od') {
              betChoice = 'Tài'
            }
            if (parlay.betType === 'ft_ou.under_od' || parlay.betType === 'hf_ou.under_od') {
              betChoice = 'Xỉu'
            }

            if (betV[0] === 'ft_ou') {
              betGoal = odd.ft_ou.handicap;
            }

            if (betV[0] === 'hf_ou') {
              betGoal = odd.hf_ou.handicap;
            }
          }

          if (parlay.betType === 'ft_1x2.home_od' || parlay.betType === 'ft_1x2.away_od' || parlay.betType === 'ft_1x2.draw_od' || parlay.betType === 'hf_1x2.home_od' || parlay.betType === 'hf_1x2.away_od' || parlay.betType === 'hf_1x2.draw_od') {

            if (parlay.betType.lastIndexOf('ft_1x2', 0) === 0) {
              betType = 'Trực tiếp FT.1X2';
            } else if (parlay.betType.lastIndexOf('hf_1x2', 0) === 0) {
              betType = 'Trực tiếp FT.1X2 / Hiệp 1';
            }
            if (parlay.betType === 'ft_1x2.home_od' || parlay.betType === 'hf_1x2.home_od') {
              betChoice = event.home
            }
            if (parlay.betType === 'ft_1x2.away_od' || parlay.betType === 'hf_1x2.away_od') {
              betChoice = event.away
            }

            if (parlay.betType === 'ft_1x2.draw_od' || parlay.betType === 'hf_1x2.draw_od') {
              betChoice = 'Hòa'
            }
          }
          return <div className="comtickets ">
            <div className="comtickets_head"><i className="icon icon-sport1"></i>
              <div className="comtickets_league">{event?.league_name}</div>
              <div className="comtickets_match">
                <div className="comtickets_home">{event?.home}</div>
                <div className="comtickets_score">{event?.ss}</div>
                <div className="comtickets_away">{event?.away}</div>
              </div>
              <div className="combtn" onClick={() => deleteBet(parlay)}><i className="icon icon-delete-outline"></i></div>
            </div>
            <div className="comtickets_status">
              <div className="comtickets_top" data-scroll="true">
                <div className="comtickets_betType">Bóng đá / {betType}</div>
                <div className="comtickets_team">
                  <div className="combetType_choice">{betChoice}</div>
                  <div className="combetType_goal">{betGoal}</div>
                </div>
              </div>
              <div className="comtickets_bottom">
                <div className={`comtickets_odds ${Number(convertMalayToDecimal(betValue)) < 0 ? 'betType-minusOdds' : ''}`}>{convertMalayToDecimal(betValue)}</div>
                <div className="comodds_type">(MY)</div>
              </div>
            </div>
          </div>
        }
      }

    }
  }

  const deleteBet = (parlay: any) => {
    setParlayData(parlayData.filter((p: any) => p.eventId !== parlay.eventId));
  }

  const hanldeFocusInput = () => {
    setFocus(true);
  }
  const convertMalayToDecimal = (value: number) => {
    if (value < 0) {
      value = -1 / value;
    }
    return Math.round((value + 1) * 100) / 100;
  }
  const handleSubmit = () => {

    if (betBalanceFormat === 0) {
      setMsg('Vui lòng nhập mức cược!');
    } else if (betBalanceFormat > Number(user.wallet)) {
      setMsg('Mức cược của bạn lớn hơn hạn mức cược hiện tại!');
    } else {
      const betInfo = {
        bet_amount: betBalanceFormat,
        bets: betParlay.map((bet: any) => {
          let betT = bet.betType.split('.');
          let type = '';
          if (betT[1] === 'home_od' || betT[1] === 'over_od') {
            type = 'home'
          }
          if (betT[1] === 'away_od' || betT[1] === 'under_od') {
            type = 'away'
          }

          if (betT[1] === 'draw_od') {
            type = 'draw'
          }
          return {
            odd_id: bet.oddId.toString(),
            bet_value: bet.betValue,
            bet_position: type === 'home' ? 0 : type === 'away' ? 1 : 2,
            bet_type: betT[0]
          }
        })
      }
      createGroup(betInfo).then(({ data }) => {
        if (data.code === 'SUCCESS') {
          setConfirm(!confirm);
          updateData(data.data.id.toString())
        } else {
          if (data.code === 'ODD_CHANGED') {
            const oddChange = betParlay.findIndex((p: any) => p.oddId === data.data.odd_id);

            betParlay[oddChange] = {
              ...betParlay[oddChange],
              betValue: data.data.newVal
            }

            setBetParlay(betParlay)
          }
          alert(data.msg);
        }
      }).catch(err => {
        console.log(err);
      })
    }

    // createBet
  }
  useEffect(() => {
    let betB = betBalance.toString();
    betB = betB.replaceAll(',', '');
    setBetBalanceFormat(Number(betB));

    // setPaytAmount(Number(betB) + (Number(betB) * Math.abs(Number(betValue))));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [betBalance])

  const reset = () => {
    setBetBalanceFormat(0)
    setProcess('process');
    setParlayData([]);
    setFocus(false);
    setBetBalance([]);
    setBetParlay([]);
    setConfirm(false);
    setMsg('');
  }
  const comconfirmMatch = (parlay: any) => {
    let league: any;
    league = live ? live.find((l: any) => l.league_id === parlay.leagueId) : null;
    !league && (league = data ? data.find((l: any) => l.league_id === parlay.leagueId) : null);
    if (league) {
      const event = league.events.find((e: any) => e.event_id === parlay.eventId);
      if (event) {
        let odd = event.odds.find((o: any) => o.odd_id === parlay.oddId);
        if (odd) {
          let betChoice;
          let betType;
          let betGoal;
          const betV = parlay.betType.split('.');
          // setTotalBet(1);

          if (parlay.betType === 'ft_hdp.home_od' || parlay.betType === 'ft_hdp.away_od' || parlay.betType === 'hf_hdp.home_od' || parlay.betType === 'hf_hdp.away_od') {
            betType = 'Trực tiếp Cược Chấp';
            if (parlay.betType === 'ft_hdp.home_od' || parlay.betType === 'hf_hdp.home_od') {
              betChoice = event.home
            }
            if (parlay.betType === 'ft_hdp.away_od' || parlay.betType === 'hf_hdp.away_od') {
              betChoice = event.away
            }
            if (betV[0] === 'ft_hdp') {
              betGoal = handicapValue(odd.ft_hdp, parlay.betType);
            }

            if (betV[0] === 'hf_hdp') {
              betGoal = handicapValue(odd.hf_hdp, parlay.betType);
            }
          }

          if (parlay.betType === 'ft_ou.over_od' || parlay.betType === 'ft_ou.under_od' || parlay.betType === 'hf_ou.over_od' || parlay.betType === 'hf_ou.under_od') {
            betType = 'Trực tiếp Tài/Xỉu';
            if (parlay.betType === 'ft_ou.over_od' || parlay.betType === 'hf_ou.over_od') {
              betChoice = 'Tài'
            }
            if (parlay.betType === 'ft_ou.under_od' || parlay.betType === 'hf_ou.under_od') {
              betChoice = 'Xỉu'
            }

            if (betV[0] === 'ft_ou') {
              betGoal = odd.ft_ou.handicap;
            }

            if (betV[0] === 'hf_ou') {
              betGoal = odd.ft_ou.handicap;
            }
          }

          if (parlay.betType === 'ft_1x2.home_od' || parlay.betType === 'ft_1x2.away_od' || parlay.betType === 'ft_1x2.draw_od' || parlay.betType === 'hf_1x2.home_od' || parlay.betType === 'hf_1x2.away_od' || parlay.betType === 'hf_1x2.draw_od') {
            betType = 'Trực tiếp FT.1X2';
            if (parlay.betType === 'ft_1x2.home_od' || parlay.betType === 'hf_1x2.home_od') {
              betChoice = event.home
            }
            if (parlay.betType === 'ft_1x2.away_od' || parlay.betType === 'hf_1x2.away_od') {
              betChoice = event.away
            }

            if (parlay.betType === 'ft_1x2.draw_od' || parlay.betType === 'hf_1x2.draw_od') {
              betChoice = 'Hòa'
            }
          }
          let ss = event?.ss.split('-')
          return <div className="comconfirm_match" data-open="false" data-status={process === 'process' ? 'waiting' : process}>
            <div className="comconfirm_match_top">
              <div className="comtickets_icon">
                {process === 'process' && <div className="preloader preloader-mini">
                  <div className="preloader_spiner">
                    <div className="preloader_circles">
                      <div className="preloader_circle-1"></div>
                      <div className="preloader_circle-2"></div>
                    </div>
                  </div>
                </div>
                }
                {process === 'accept' && <div className="iconani-done" data-animate="true"><SVG src={iconSuccess} /></div>}
                {process === 'reject' && <div className="iconani-warn" data-animate="true"><SVG src={iconReject} /></div>}
              </div><i className="icon icon-sport1"></i>
              <div className="comconfirm_team"><span>{event?.home}</span><span>{event?.away}</span></div>
              <div className="comconfirm_score"><span>{ss[0]}</span><span>{ss[1]}</span></div>
            </div>
            <div className="comconfirm_match_bottom">
              <div className="comconfirm_match_bettype"><span>Bóng đá / {betType}</span><span>{betChoice}&nbsp;&nbsp;
                {betGoal}</span></div>
              <div className="comconfirm_match_oddsbox"><span className="comconfirm_match_odds">{parlay.betValue}</span><span
                className="comconfirm_match_type">(Dec)</span></div><i className="icon icon-arrow-bottom"></i>
              <div className="comconfirm_match_collapse">
                <div className="comconfirm_match_league">{event?.league_name}</div>
              </div>
            </div>
          </div>
        }
      }
    }

  }
  const comConfirm = () => {
    return <div className="comconfirm" data-status={confirm ? 'is-open' : ''} style={{
      position: 'absolute',
      top: '5px',
      height: '100%'
    }}>
      <div className="comconfirm_status" data-status={process}>
        <div className="comconfirm_process">
          <div className="comconfirm_text">
            {process === 'process' && <>
              <div className="comtickets_icon">
                <div className="preloader preloader-mini">
                  <div className="preloader_spiner">
                    <div className="preloader_circles">
                      <div className="preloader_circle-1"></div>
                      <div className="preloader_circle-2"></div>
                    </div>
                  </div>
                </div>
              </div>
              <p>Cược đang diễn ra...</p></>}
            {process === 'accept' && <><div className="comtickets_icon">
              <div className="iconani-done" data-animate="true"><SVG src={iconSuccess} /></div>
            </div>
              <p>Đơn cược đã được chấp nhận.</p>
            </>}
            {process === 'reject' && <><div className="comtickets_icon">
              <div className="iconani-warn" data-animate="true"><SVG src={iconReject} /></div>
            </div>
              <p>Đơn cược bị hủy.</p>
            </>}
          </div>
          <div className="comconfirm_matchlist">
            {parlayData.map((parlay: any, idx: any) => {
              return <div key={idx}>{comconfirmMatch(parlay)}</div>
            })}
          </div>
        </div>
        <div className="comconfirm_detail">
          <div className="comtickets_item">
            <div className="comtickets_title">Cược {parlayData.length} (1 Đặt cược)</div>
            <div className="comtickets_info">
              <div className="comtickets_title">Tỷ lệ cược:</div>
              <div className="comtickets_value">{Number(totalBet).toFixed(2)}</div>
            </div>
            <div className="comtickets_value">2</div>
          </div>
        </div>
        <div className="comconfirm_total">
          <div className="comtickets_item">
            <div className="comtickets_title">Tổng đặt cược</div>
            <div className="comtickets_value">1</div>
          </div>
          <div className="comtickets_item">
            <div className="comtickets_title">Tổng cược</div>
            <div className="comtickets_value">{betBalanceFormat.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
          </div>
        </div>
        <div className="combtn-box">
          <div className="combtn btn-highlight" js-btn-close="" onClick={() => {
            setShowParlay(false)
            reset();
          }}>Đặt cược vào các sự kiện khác.</div>
          <div className="combtn btn-highlight" onClick={() => {
            setShowParlay(false);
            setAppBarSelect('statement')
            reset()
          }}><span className="c-text">Kiểm tra Sao kê</span>
            <div className="c-badge-group"></div>
          </div>
        </div>
      </div>
    </div>
  }
  return <div id="pageParlay">
    <div className="page page-parlay" data-status={showParlay ? 'is-open' : ''} >
      <div className="page_header">
        <div className="main-bar" style={{ backgroundColor: '#5F86C5 !important' }}>
          <div className="btn" onClick={() => setShowParlay(false)}><i className="icon icon-back"></i></div>
          <div className="title">Phiếu Đặt Cược</div>
          <div className="badge badge-count">{parlayData.length}</div>
          {!confirm && <div className="btn btn-light" onClick={() => setParlayData([])}><i className="icon icon-delete-outline"></i>Xóa tất cả</div>}
        </div>
      </div>
      <div className="page_content">
        <div className="comalert">
          <div className="comalert_text"></div>
        </div>
        <div className="comparlayBet_panel" data-scroll="false">
          <div className="comparlay_container" data-status={!confirm ? 'is-open' : ''}>
            <div className="comtickets_group">
              {parlayData.map((parlay: any, idx: Key | null | undefined) => {
                return <div key={idx}>{comtickets(parlay)}</div>
              })}

            </div>
            {parlayData.length < 3 && <div className="comtickets comtickets_less">
              <div className="comtickets_text">Thêm 1 trận đấu nữa để hoàn thành vé cược xiên.</div>
              <div className="combtn btn-primary" js-parlay-back="" onClick={() => { setShowParlay(false); setFocus(false); setBetBalance([]); setBetBalanceFormat(0) }}>Duyệt các sự kiện khác</div>
            </div>}
          </div>
          <div className="comparlay_betting" data-status={parlayData.length >= 3 && !confirm ? 'is-open' : ''} >
            <div className="comtickets_bet">
              <div className="comtickets_keyin">
                <div className="comparlay_block comparlay-show" data-status={focus ? 'is-open' : ''}>
                  <div className="">
                    <div className="comtickets_input" data-disabled="false"><span>Cược {parlayData.length} (1 Đặt cược)<div className="comtickets_info">
                      <div className="comtickets_title">Tỷ lệ cược </div>
                      <div className="comtickets_value">{Number(totalBet).toFixed(2)}</div>
                    </div></span>
                      <div id="tooltips" className="tooltips tooltips-bottom tooltips-warning" data-status={msg !== '' ? 'is-open' : ''}>
                        <div className="tooltips_arrow"></div>
                        <div id="tipContent" className="tooltips_content">{msg}</div>
                      </div>
                      <div className="textfield textfield-stake" data-focus={focus} data-selected="false">
                        <div className="textfield_currency">UT</div>
                        <div className="textfield_input" onClick={() => hanldeFocusInput()}>
                          {betBalanceFormat > 0 && <>{betBalanceFormat.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</>}
                        </div>
                        <div className="btn btn-cancel" onClick={() => setBetBalance([])}><i className="icon icon-cancel"></i></div>
                      </div>
                    </div>
                  </div>
                  <div className="comtickets_info">
                    <div className="comtickets_item">
                      <div className="comtickets_title">Tối thiểu:</div>
                      <div className="comtickets_value">{user.bongdamin}</div>
                    </div>
                    <div className="comtickets_item">
                      <div className="comtickets_title">Tối đa:</div>
                      <div className="comtickets_value">{user.runing_amount > user.bongdamax ? user.bongdamax : user.runing_amount}</div>
                    </div>
                    <div className="comtickets_item">
                      <div className="comtickets_title">Thanh toán:</div>
                      <div className="comtickets_value">{betBalanceFormat.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
                    </div>
                  </div>
                  <div className="comkeypad" data-status={focus ? 'is-open' : ''}>
                    <div className="comkeypad_left">
                      <div className="comkeypad_lefttop">
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '1'])}>1</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '2'])}>2</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '3'])}>3</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '4'])}>4</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '5'])}>5</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '6'])}>6</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '7'])}>7</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '8'])}>8</div>
                        <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '9'])}>9</div>
                        <div className="comkeypad_item" onClick={() => betBalance.length > 0 && setBetBalance([...betBalance, '0'])}>0</div>
                      </div>
                      <div className="comkeypad_leftbottom">
                        <div className="comkeypad_item keypad-dark" onClick={() => {
                          const newBalance = betBalanceFormat + 1;
                          setBetBalanceFormat(Number(newBalance))
                          setBetBalance(Array.from(newBalance.toString()))
                        }}>+1</div>
                        <div className="comkeypad_item keypad-dark" onClick={() => {
                          const newBalance = betBalanceFormat + 10;
                          setBetBalanceFormat(Number(newBalance))
                          setBetBalance(Array.from(newBalance.toString()))
                        }}>+10</div>
                        <div className="comkeypad_item keypad-dark" onClick={() => {
                          const newBalance = betBalanceFormat + 100;
                          setBetBalanceFormat(Number(newBalance))
                          setBetBalance(Array.from(newBalance.toString()))
                        }}>+100</div>
                      </div>
                    </div>
                    <div className="comkeypad_right">
                      <div className="comkeypad_item" onClick={() => {
                        betBalance.pop();
                        setBetBalance([...betBalance]);
                      }}><span className="comkeypad_caculator"><i className="icon icon-backspace"></i></span>
                      </div>
                      <div className="comkeypad_item" onClick={() => betBalance.length > 0 && setBetBalance([...betBalance, '00'])}>00</div>
                      <div className="comkeypad_item keypad-dark" onClick={() => {
                        // setBetBalance([]);
                        setBetBalanceFormat(Number(user.wallet))
                        setBetBalance(Array.from(user.wallet.toString()))
                      }}>Tối đa</div>
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <div className="comtickets_info">
                  <div className="comtickets_item">
                    <div className="comtickets_title">Tổng đặt cược:</div>
                    <div className="comtickets_value">{betBalanceFormat > 0 ? 1 : 0}</div>
                  </div>
                  <div className="comtickets_item">
                    <div className="comtickets_title">Tổng cược:</div>
                    <div className="comtickets_value"> {betBalanceFormat.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
                  </div>
                  <div className="comtickets_item">
                    <div className="comtickets_title">Thanh toán:</div>
                    <div className="comtickets_value">{betBalanceFormat.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
                  </div>
                </div>
                <div className="comtickets_info">
                  <div className="comtickets_item">
                    <div className="comtickets_title">Hạn mức còn lại:</div>
                    <div className="comtickets_value">{user.wallet}</div>
                  </div>
                </div>
              </div>
              <div className="combetting">
                <div className="combtn btn-bet" onClick={() => handleSubmit()}>Đặt Cược</div>
              </div>
            </div>
          </div>
          {comConfirm()}
        </div>
      </div>
    </div>
  </div>
}