import { Suspense } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import Sports from "../Sports";
import LiveMatch from "../Sports/LiveMatch";
import Statistics from "../Sports/Statistics";
import { LayoutSplashScreen } from "./CoreSplashScreen";
import Layout from "./Layout";

export default function BasePage() {
  return (
    <Routes>
      <Route path="/lobby" element={
        <Suspense fallback={<LayoutSplashScreen />}>
          <Layout />
        </Suspense>} />
      <Route path='/sports' element={<Sports />} />
      <Route path='/statistics/:matchId' element={<Statistics />} />
      <Route path='/livematch/:matchId' element={<LiveMatch />} />
      <Route
        path="/"
        element={<Navigate to="/lobby" replace />}
      />
    </Routes>

  );
}
