import Event from "./Event";

/* eslint-disable jsx-a11y/anchor-is-valid */
const Detail = ({ league }: any) => {

  return <>
    <ul className="collapse-group result-group">
      <li>
        <div className="heading"><a className="toggle" aria-expanded="true">
          {/* <div className="comleague_flag"><img src="https://mbi.vixcdn.com/leagueimg/l_43.png" /></div> */}
          <div className="league-name"><span>{league.name}</span></div>
          <i className="icon icon-arrow-bottom"></i>
        </a></div>
        <div className="collapse in">
          <ul className="collapse-group result-content">
            {league.events.map((event: any, i: number) => {
              return <Event key={i} event={event} />
            })}
          </ul>
        </div>
      </li>
    </ul>
  </>

}
export default Detail;