import moment from "moment";
import { useEffect, useState } from "react";
import { satementReport } from "../statementCrud";

const WinLoss = (props: any) => {
  const { switchBar } = props;
  const [bets, setBets] = useState(null as any);
  const [loading, setLoading] = useState(true);
  const [week, setWeek] = useState(null as any);
  const [openWeek, setOpenWeek] = useState(false);
  const [openLastWeek, setOpenLastWeek] = useState(false);
  const [lastWeek, setLastWeek] = useState(null as any);
  useEffect(() => {
    if (!switchBar) {
      satementReport().then(({ data }: any) => {
        setBets(data.data);
        let weekCount = 0;
        let weekCommission = 0;
        let weekStake = 0;
        let weekProfit = 0;

        let lastWeekCount = 0;
        let lastWeekCommission = 0;
        let lastWeekStake = 0;
        let lastWeekProfit = 0;

        data.data.week.forEach((week: any) => {
          weekCount += week.count;
          weekCommission += week.commission;
          weekStake += week.stake;
          weekProfit += week.profit;
        })

        data.data.lastWeek.forEach((lastWeek: any) => {
          lastWeekCount += lastWeek.count;
          lastWeekCommission += lastWeek.commission;
          lastWeekStake += lastWeek.stake;
          lastWeekProfit += lastWeek.profit;
        })

        setWeek({
          count: weekCount,
          commission: weekCommission,
          stake: weekStake,
          profit: weekProfit,
        })

        setLastWeek({
          count: lastWeekCount,
          commission: lastWeekCommission,
          stake: lastWeekStake,
          profit: lastWeekProfit,
        })
        setLoading(false)
      })
    } else {
      setBets(null)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [switchBar])
  const perLoader = () => {
    return <div className="preloader preloader-min">
      <div className="preloader_spiner">
        <div className="preloader_circles">
          <div className="preloader_circle-1"></div>
          <div className="preloader_circle-2"></div>
        </div>
      </div>
    </div>
  }
  return <>
    {loading && perLoader()}
    {bets && <><div className="fold-total" data-open={openWeek}>
      <div className="fold-total__header">
        <div className="fold-total__row--title">
          <div className="text-time">Tổng Tuần này<span className="text-amount">({week?.count})</span>
            <i className="icon icon-arrow-top" onClick={() => setOpenWeek(!openWeek)}></i>
          </div>
        </div>
        <div className="fold-total__row--total">
          <div className="info">
            <div className="info_title">Tổng Hoa hồng:</div>
            <div className="info_value">{Number(week?.commission).toFixed(2)}</div>
          </div>
          <div className="info">
            <div className="info_title">Lượng tiền:</div>
            <div className="info_value">{Number(week?.stake).toFixed(2)}</div>
          </div>
          <div className="info">
            <div className="info_title">Tổng Thắng/Thua:</div>
            <div className="info_value"><span className={`text-${Number(week?.profit) === 0 ? 'draw' : Number(week?.profit) < 0 ? 'loss' : 'win'}`}>{Number(week?.profit).toFixed(2)}</span></div>
          </div>
        </div>
      </div>
      <div className="fold-total__container">
        {bets.week.map((week: any, idx: number) => {
          return <div key={idx} className="fold fold--unexpand" style={{ display: 'block' }}>
            <div className="fold_header">
              <div className="fold_item touch">
                <div className="text-time">
                  {moment(week.date).format('DD/MM/YYYY h:m A')} <span className="text-amount">({week.count})</span>
                  <i className="icon icon-arrow-top"></i>
                </div>
                <div className="info">
                  <div className="info_title">Tiền cược:</div>
                  <div className="info_value">{week.stake}</div>
                </div>
                <div className="info">
                  <div className="info_title">Thắng / Thua:</div>
                  <div className="info_value"><span className={`text-${Number(week?.profit) === 0 ? 'draw' : Number(week?.profit) < 0 ? 'loss' : 'win'}`}>{Number(week?.profit).toFixed(2)}</span></div>
                </div>
              </div>
            </div>
          </div>
        })}

      </div>
    </div>
      <div className="fold-total" data-open={openLastWeek}>
        <div className="fold-total__header">
          <div className="fold-total__row--title">
            <div className="text-time">Tổng Tuần trước<span className="text-amount">({lastWeek?.count})</span>
              <i className="icon icon-arrow-top" onClick={() => setOpenLastWeek(!openLastWeek)}></i>
            </div>
          </div>
          <div className="fold-total__row--total">
            <div className="info">
              <div className="info_title">Tổng Hoa hồng:</div>
              <div className="info_value">{Number(lastWeek?.commission).toFixed(2)}</div>
            </div>
            <div className="info">
              <div className="info_title">Lượng tiền:</div>
              <div className="info_value">{Number(lastWeek?.stake).toFixed(2)}</div>
            </div>
            <div className="info">
              <div className="info_title">Tổng Thắng/Thua:</div>
              <div className="info_value"><span className={`text-${Number(lastWeek?.profit) === 0 ? 'draw' : Number(lastWeek?.profit) < 0 ? 'loss' : 'win'}`}>{Number(lastWeek?.profit).toFixed(2)}</span></div>
            </div>
          </div>
        </div>
        <div className="fold-total__container">
          {bets.lastWeek.map((week: any, idx: number) => {
            return <div key={idx} className="fold fold--unexpand" style={{ display: 'block' }}>
              <div className="fold_header">
                <div className="fold_item touch">
                  <div className="text-time">
                    {moment(week.date).format('DD/MM/YYYY h:m A')} <span className="text-amount">({week.count})</span>
                    <i className="icon icon-arrow-top"></i>
                  </div>
                  <div className="info">
                    <div className="info_title">Tiền cược:</div>
                    <div className="info_value">{week.stake}</div>
                  </div>
                  <div className="info">
                    <div className="info_title">Thắng / Thua:</div>
                    <div className="info_value"><span className={`text-${Number(week?.profit) === 0 ? 'draw' : Number(week?.profit) < 0 ? 'loss' : 'win'}`}>{Number(week?.profit).toFixed(2)}</span></div>
                  </div>
                </div>
              </div>
            </div>
          })}
        </div>
      </div>
    </>
    }
  </>
}

export default WinLoss