import axios from "axios";

export function getInPlayLeagues(game_type: String) {
  // Authorization head should be fulfilled in interceptor.
  return axios.get(process.env.REACT_APP_API_URL + "/league/getInPlayLeagues?type=single&game_type="+ game_type);
}

export function getUpcomingPlayLeagues(game_type: String) {
  return axios.get(process.env.REACT_APP_API_URL + "/league/getUpcomingPlayLeagues?type=single&game_type="+ game_type);
}

export function getLive(mathId: string, liveId: string) {
  return axios.get(process.env.REACT_APP_API_URL + "/league/live/" + mathId + "/" + liveId);
}

export function createBet(data: any) {
  return axios.post(process.env.REACT_APP_API_URL + '/bet', data);
}


export function createGroup(data: any) {
  return axios.post(process.env.REACT_APP_API_URL + '/betGroup', data);
}

export function inPlayById(id: any) {
  return axios.get(process.env.REACT_APP_API_URL + '/bet/inPlayById/' + id);
}

