import axios from "axios";

export const LOGIN_URL = process.env.REACT_APP_API_URL + "/auth/login";

export const ME_URL = "user-info";

export function login(username: string, password: string) {
  return axios.post(LOGIN_URL, { username, password });
}


export function getUserByToken() {
  // Authorization head should be fulfilled in interceptor.
  return axios.get(process.env.REACT_APP_API_URL + "/" + ME_URL);
}

export function changePassword(password: string, changepassword: string) {
  return axios.post(process.env.REACT_APP_API_URL + '/changepwd', { password, changepassword });
}
