import moment from "moment";
import { useState } from "react";
import BetDetail from "./BetDetail";

const Fold = (props: any) => {
  const [loadingFold, setLoadingFold] = useState(false);
  const [foldOpen, setfoldOpen] = useState(false);
  const { bet, setSelected, idx } = props;

  return <div className="fold" data-status={foldOpen ? 'is-open' : ''}>
    <div className="fold_header">
      <div className="fold_item touch">
        <div className="text-time">
          {moment(bet.date).format('YYYY/MM/DD').toString() ?? ''} <span className="text-amount"> ({bet.count ?? 0}) </span>
          <i className="icon icon-arrow-top" onClick={() => {
            setfoldOpen(!foldOpen)
            // setLoadingFold(!loadingFold)
          }}></i>
        </div>
        <div className="info">
          <div className="info_title">Tiền cược:</div>
          <div className="info_value">{bet.stake ?? 0}</div>
        </div>
        <div className="info">
          <div className="info_title">Thắng / Thua:</div>
          <div className="info_value"><span className={`text-${Number(bet.profit) === 0 ? 'draw' : Number(bet.profit) < 0 ? 'loss' : 'win'}`}>{bet.profit}</span></div>
        </div>
        <div className="info">
          <div className="info_title">Hoa hồng:</div>
          <div className="info_value"><span className={`text-${Number(bet.commission) === 0 ? 'draw' : Number(bet.commission) < 0 ? 'loss' : 'win'}`}>{bet.commission}</span></div>
        </div>
      </div>
    </div>
    <div className="fold_container">
      {loadingFold && <div className="preloader preloader-min">
        <div className="preloader_spiner">
          <div className="preloader_circles">
            <div className="preloader_circle-1"></div>
            <div className="preloader_circle-2"></div>
          </div>
        </div>
      </div>}
      <BetDetail bet={bet} setSelected={setSelected} loadingFold={loadingFold} foldOpen={foldOpen} setLoadingFold={setLoadingFold} />
    </div>
  </div>
}

export default Fold;