/* eslint-disable jsx-a11y/anchor-is-valid */
import { useEffect, useState } from "react";
import BetRunning from "./BetRunning";
import WinLoss from "./WinLoss";

const Done = (props: any) => {
  const { bets, loading, tabDone, setTabDone, setSelected } = props;
  const [total, setTolal] = useState(null as any)
  const [switchBar, setSwitchBar] = useState(true)
  useEffect(() => {
    if (bets?.data.bets) {

      let count = 0;
      let stake = 0;
      let commission = 0;
      let profit = 0;
      bets?.data.bets.forEach((bet: any) => {
        count += bet.count;
        stake += bet.stake;
        commission += bet.commission;
        profit += bet.profit;
      })

      setTolal({ count, stake, commission, profit });
    }
  }, [bets])
  const infoDoneHeader = () => {
    return <div>
      <div className="btn-bar">
        <div className="btn-group">
          <div className="btn btn-outline" data-status={tabDone === 'today' ? 'is-active' : ''} onClick={() => { setTabDone('today'); setSwitchBar(true); }}>
            <div className="btn_text">Hôm nay</div>
          </div>
          <div className="btn btn-outline" data-status={tabDone === '2before' ? 'is-active' : ''} onClick={() => { setTabDone('2before'); setSwitchBar(true); }}>
            <div className="btn_text">2 ngày qua</div>
          </div>
          <div className="btn btn-outline" data-status={tabDone === '7before' ? 'is-active' : ''} onClick={() => { setTabDone('7before'); setSwitchBar(true); }}>
            <div className="btn_text">7 ngày qua</div>
          </div>
        </div>
      </div>
      <div className="info_header">

        <div className="page_infobar">
          <div className="text-time"></div>
          <div className="info">
            <div className="info_title"> Số Dư Ban Đầu:</div>
            <div className="info_value">0.00</div>
          </div>
        </div>
        {tabDone === '7before' && <><div className="c-switch-bar">
          <a className="c-btn" data-selected={switchBar ? 'true' : 'false'} onClick={() => setSwitchBar(!switchBar)}>
            <span className="c-text">Bình thường</span>
          </a>
          <a className="c-btn" data-selected={!switchBar ? 'true' : 'false'} onClick={() => setSwitchBar(!switchBar)}>
            <span className="c-text">Báo cáo Thắng/Thua</span>
          </a>
        </div>
        </>}
        <div className="page_ads"></div>
        <div className="page_totalbar">
          {switchBar && <> <div className="dropdown">
            <div className="btn" data-toggle="dropdown" aria-expanded="false">
              <div className="btn_text">Thể Thao</div>
            </div>
            <ul className="dropdown-menu" role="menu">
              <li className="active"><a>Thể Thao</a></li>
              <li className=""><a>Giao dịch</a></li>
            </ul>
          </div>
            <div className="info">
              <div className="info_title">Tổng cược:</div>
              <div className="info_value">{total?.count ?? 0}</div>
            </div>
            <div className="info">
              <div className="info_title">Thắng / Thua:</div>
              <div className="info_value"><span className={`text-${total?.profit >= 0 ? 'win' : 'loss'}`}>{Number(total?.profit).toFixed(2)}</span></div>
            </div>
            <div className="info">
              <div className="info_title">Hoa hồng:</div>
              <div className="info_value"><span className={`text-${total?.commission >= 0 ? 'win' : 'loss'}`}>{Number(total?.commission).toFixed(2)}</span></div>
            </div></>
          }
          {!switchBar && <div className="dropdown dropdown--full">
            <div className="btn" data-toggle="dropdown" aria-expanded="false">
              <div className="btn_text">Tất Cả</div>
            </div>
            <ul className="dropdown-menu" role="menu">
              <li className="active"><a>Tất Cả</a></li>
              <li className=""><a>Thể Thao</a></li>
            </ul>
          </div>}
        </div>
      </div>
    </div>
  }
  return <>
    {infoDoneHeader()}
    {switchBar && <BetRunning bets={bets} loading={loading} setSelected={setSelected} />}
    {!switchBar && <WinLoss switchBar={switchBar}/>}
  </>
}

export default Done;