import { useState } from "react";
import Bet from "../Bet";
import { Leagues } from "./Leagues";

const Odds = (props: any) => {
  const [openAll, setOpenAll] = useState(true);
  const [bet, setBet] = useState(null as any);
  const { parlay, setParlayData, parlayData, setAppBarSelect, data, gameType } = props;
  return <>
    <div className="content" id="ContentScoll" style={{ paddingTop: '98px' }}>
      <div className="commarket" data-status={openAll ? 'is-open' : ''}>
        <div className="commarket_header">
          Thu hẹp tất cả giải đấu
          <div className="icon icon-arrow-bottom" onClick={() => setOpenAll(!openAll)}></div>
        </div>
        {data.length > 0 && data.map((league: any, idx: any) => {
          return <Leagues open={openAll} gameType={gameType} league={league} key={idx} setBet={setBet} tab={props.tab} parlay={parlay} setParlayData={setParlayData} parlayData={parlayData} />
        })}
      </div>
    </div>
    {(bet && data.length > 0) && <Bet bet={bet} setBet={setBet} leagues={data} setParlayData={setParlayData} parlayData={parlayData} setAppBarSelect={setAppBarSelect} />}
  </>
}

export default Odds;