import { useParams } from "react-router-dom";
import AppBar from "./AppBar";

const Statistics = () => {
  let { matchId } = useParams();
  
  return <>
    <div id="overlayComponents" style={{ display: 'block' }}>
      <div id="pagePanel" className="contactUsPanel">
        <div className="page page-contactus" data-status="is-open">
          <div className="page_header">
            <div className="main-bar">
              <div className="title">Statistics</div>
            </div>
          </div>
          <div className="page_content">
            <iframe src={`https://ls.1266366.com/index.aspx?clientid=846&flag=ls&language=vi&clientmatchid=${matchId}`} width="100%" height="100%" scrolling="no"></iframe>
          </div>
        </div>
      </div>
    </div>
    <AppBar navigate={true} />
  </>
}

export default Statistics;