import { useEffect, useState } from "react"
import { Matchs } from "./Matchs"

/* eslint-disable jsx-a11y/anchor-is-valid */
export const Leagues = (props: any) => {
  const [openL, setOpenL] = useState(false);
  const { league, parlay, setParlayData ,parlayData, gameType} = props
  const [events, setEvents] = useState([] as any)
  useEffect(() => {
    let _events: any[] = [];
    if (parlay) {
      league.events.forEach((event: any) => {
        const e: any = event;
        const _eo: any = event.odds.filter((odd: any) => odd.is_parlay === true);
        e.odds = _eo;
        _events.push(e);
        setEvents(_events)
      })
    } else {
      setEvents(league.events)
    }

    return () => setEvents([]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [parlay,league])
  useEffect(() => {
    setOpenL(false);
  },[props.open])
  const header = () => {
    return <div className="comleague_header" onClick={() => setOpenL(!openL)}>
      <div className="comleague_flag">
        {/* <img src="https://mbi.vixcdn.com/leagueimg/l_53899.png" style="display: block;"> */}
      </div>
      <div className="comleague_name">
        {league.name}
        <span className="comleague_number">({league.events.length})</span>
      </div>
      <a className="c-btn c-btn--myfav" data-added="false">
        <i className="c-icon c-icon--star-outline"></i>
      </a>
      <div className="btn btn-refresh"><i className="icon icon-refresh-single"></i></div>
    </div>
  }

  return <div className={`comleague ${props.tab === 'live' ? 'is-live' : ''}`} data-status={props.open ? 'is-open' : ''}>
    {header()}
    <div className="commatch_list" style={{ display: (props.open || openL) ? 'flex' : 'none', overflow: 'visible' }}>
      <div>
        {events.map((event: any, idx: any) => {
          return <div key={idx}>{event.odds && <Matchs league={league} match={event} key={idx} openAll={props.open} setBet={props.setBet} tab={props.tab} parlay={parlay} setParlayData={setParlayData} parlayData={parlayData} gameType={gameType}/>}</div>
        })}
      </div>
    </div>

  </div>
}