import { useFormik } from "formik";
import { useEffect, useState } from "react";
import { changePassword } from "../Auth/_redux/authCrud";
import * as Yup from "yup";
const ChangePassword = (props: any) => {
  const [initialValues, setInitialValues] = useState({
    password: "",
    changepassword: "",
  });
  useEffect(() => {
    setInitialValues({
      password: "",
      changepassword: "",
    })
  }, [props.openChangePassword])
  const [loading, setLoading] = useState(false);
  const enableLoading = () => {
    setLoading(true);
  };

  const disableLoading = () => {
    setLoading(false);
  };
  const ChangePwdSchema = Yup.object().shape({
    password: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required('Vui lòng nhập mật khẩu'),
    changepassword: Yup.string()
      .min(3, "Minimum 3 symbols")
      .max(50, "Maximum 50 symbols")
      .required('Vui lòng nhập xác nhận mật khẩu')
      .when("password", {
        is: (val: any) => (val && val.length > 0 ? true : false),
        then: Yup.string().oneOf(
          [Yup.ref("password")],
          "Cả hai mật khẩu cần phải giống nhau"
        )
      }),
  });
  const formik: any = useFormik({
    initialValues,
    validationSchema: ChangePwdSchema,
    onSubmit: (values, { setStatus, setSubmitting }) => {
      enableLoading();
      setTimeout(() => {
        changePassword(values.password, values.changepassword)
          .then(({ data }: any) => {
            disableLoading();
            alert(data.message);
            setSubmitting(false);
            props.setOpenChangePassword(false);
          })
          .catch(() => {
            disableLoading();
            setSubmitting(false);
            setStatus('Tên hoặc mật khẩu không hộ lệ');
          });
      }, 1000);
    },
  });
  return <><div id="divPasswordEdit" className="page page-password" data-full="" data-status={props.openChangePassword ? 'is-open' : ''}>
    <div className="page_header">
      <div className="main-bar">
        <div className="btn" onClick={() => props.setOpenChangePassword(!props.openChangePassword)}><i className="icon icon-back"></i></div>
        <div className="title">Cài Đặt Mật Khẩu</div>
      </div>
    </div>
    <div className="page_content page-edit">
      <form onSubmit={formik.handleSubmit}>
        <div className="content_group">
          <div className="content_row">
            <div className="col_title">Mật khẩu mới </div>
            <div className="textfield "><input id="chInputPassword" className="textfield_input password-input" type="password"
              placeholder="Mật khẩu mới  "  {...formik.getFieldProps("password")} /></div>
            <div id="divPwErrorMsg" className="tooltips tooltips-bottom tooltips-warning" data-status={formik.errors.password ? 'is-open' : ''}>
              <div className="tooltips_arrow"></div>
              <div className="tooltips_content"></div>
            </div>
          </div>
          <div className="content_row">
            <div className="col_title">Xác nhận mật khẩu</div>
            <div className="textfield ">
              <input className="textfield_input password-input" type="password"
                placeholder="Xác nhận mật khẩu"  {...formik.getFieldProps("changepassword")} /></div>
            <div id="divCnErrorMsg" className="tooltips tooltips-bottom tooltips-warning" data-status={formik.errors.changepassword ? 'is-open' : ''}>
              <div className="tooltips_arrow"></div>
              <div className="tooltips_content">{formik.errors.changepassword}</div>
            </div>
          </div>
          <div className="content_row">
            <button type="submit" disabled={formik.isSubmitting} className="btn btn-confirm-edit"><span className="btn_text">Thay đổi</span></button>
          </div>
        </div>
      </form>
      <div className="content_note"><span className="text-mark">* </span><span>Tối thiểu 6 ký tự (tối đa 10) và cAsE
        sEnSiTiVe.</span></div>
      <div className="content_group">
        <div id="Gesture" className="content_row touch" data-status="">
          <div className="col_title">Mật khẩu cử chỉ</div>
          <div className="col_value">
            <div className="btn">
              <div className="btn-switch"><span></span></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
    <div className="loading" style={{ display: loading ? 'block' : 'none', zIndex: 1111 }}>
      <i className="icon-loading"><span></span><span></span><span></span><span></span></i></div>
  </>
}

export default ChangePassword;