import { getStorage, setStorage } from "../Helpers/LocalStorageHelpers";

export const actionTypes = {
  setThemeDark: "[App] Set ThemeDark"
};

const node = document.createAttribute("data-darkmode");
node.value = (getStorage('theme') ? getStorage('theme') === 'dark' ? true : false : false).toString();
document.documentElement.attributes.setNamedItem(node);

const initialAppState: any = {
  themeDark: getStorage('theme') ? getStorage('theme') === 'dark' ? true : false : false,
};

export const reducer = (state: any = initialAppState, action: any) => {
  switch (action.type) {
    case actionTypes.setThemeDark: {
      const { themeDark } = action.payload;
      setStorage('theme', themeDark ? 'dark' : 'light', null);
      // const node = document.createAttribute("data-darkmode");
      node.value = themeDark.toString();
      document.documentElement.attributes.setNamedItem(node);      
      return { themeDark };
    }

    default:
      return state;
  }
};

export const actions: any = {
  setThemeDark: (themeDark: any) => ({ type: actionTypes.setThemeDark, payload: { themeDark } })
};

