/* eslint-disable jsx-a11y/anchor-is-valid */
import { useState } from "react";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { actions } from "../App/Redux";
import { getStorage } from "../Helpers/LocalStorageHelpers";

const LeftMenu = (props: any) => {
  const { user } = useSelector(
    (state: any) => ({ user: state.auth.user }),
    shallowEqual
  );

  const dispatch = useDispatch();

  const [isMoreSport, setIsMoreSport] = useState(false);
  const [isTab, setIsTab] = useState('sport');
  const [themeDark, setThemeDark] = useState(getStorage('theme') ? getStorage('theme') === 'dark' ? true : false : false);

  const handleChangeTheme = () => {
  
    // console.log(document.getElementsByTagName('html').getAttribute());
    dispatch(actions.setThemeDark(!themeDark));
    setThemeDark(!themeDark);
  }
  

  const navigate = useNavigate();
  return <div id="LeftMenu">
    <div className="side-bar">
      <div className="overlay" data-status={props.openMenu ? 'is-open' : ''} onClick={() => props.setOpentMenu(!props.openMenu)}></div>
      <div className="side-nav" data-status={props.openMenu ? 'is-open' : ''}>
        <div className="side-nav_header">
          <div className="side-nav_info">
            <div className="btn btn-side-info"><i className="icon icon-my-account-outline"></i>
              <div className="side-info_text"><span className="btn_text">{user.username}</span><span className="btn_text text-balance">UT 
                {Number(user.wallet).toFixed(2)}</span></div>
            </div>
            <div className="btn btn-side-logout"><i className="icon icon-logout"></i><span className="btn_text">Thoát</span></div>
          </div>
        </div>
        <div className="side-nav_content-switch">
          <div className="side-switch-ver">
            <div className="side-switch-ver_header"><i className="icon icon-switch"></i><span className="text">Phiên bản</span></div>
            <div className="side-switch-ver_btns"><a id="switchCompact" className="btn" data-status="is-active"><span
              className="btn_text">Rút gọn</span></a><a id="switchClassic" className="btn"><span className="btn_text">Tiêu
                chuẩn</span></a></div>
          </div>
        </div>
        <div className="side-nav_content-account">
          <div className="side-account">
            <div className="side-account_item">
              <div className="btn" onClick={() => navigate('/lobby')}><span className="side-account_icon"><i className="icon icon-home-outline"></i></span><span
                className="btn_text">Trang Chủ</span></div>
            </div>
            <div className="side-account_item">
              <div className="btn"><span className="side-account_icon"><i className="icon icon-email"></i></span><span
                className="btn_text">Tin nhắn</span></div>
            </div>
            <div className="side-account_item">
              <div className="btn"><span className="side-account_icon"><i className="icon icon-contact-us"></i></span><span
                className="btn_text">Liên hệ</span></div>
            </div>
            <div className="side-account_item">
              <div className="btn" id="darkMode" data-status={themeDark ? 'is-active' : ''} onClick={handleChangeTheme}><span className="side-account_icon"><i
                className="icon icon-darkmode"></i></span><span className="btn_text">Chế độ nền tối</span></div>
            </div>
          </div>
        </div>
        <div className="side-nav_banner">
          <div className="side-banner"><img
            src="https://mbi.vixcdn.com/Content/public/Promotion_Menu/vn/pm_esports.jpg?v=202202172324" /></div>
        </div>
        <div className="side-nav_content-hot">
          <div className="side-nav_menu">
            <div className="side-nav_item">
              <div className="btn"><i className="icon icon-sport-world-cup"></i><span className="btn_text">Cúp Thế Giới</span></div>
            </div>
          </div>
        </div>
        <div className="side-nav_content-main">
          <div className="side-nav_tabs">
            <ul className="tab">
              <li data-status={isTab === 'sport' ? 'is-active' : ''} onClick={() => setIsTab('sport')}>
                <div className="btn">Thể Thao</div>
              </li>
              <li data-status={isTab === 'parlay' ? 'is-active' : ''} onClick={() => setIsTab('parlay')}>
                <div className="btn">Cược xiên</div>
              </li>
              <li data-status="">
                <div className="btn">Sòng bạc</div>
              </li>
              <li data-status="">
                <div className="btn">Khác</div>
              </li>
            </ul>
          </div>
          <div className="side-nav_group">
            <div>
              <div className="side-nav_menu" data-status={isTab === 'sport' ? 'is-open' : ''} data-more={isMoreSport ? 'is-open' : ''}>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport1"></i><span className="btn_text">Bóng đá</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">1151</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport997"></i><span className="btn_text">Bóng Đá Saba</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">36</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport43"></i><span className="btn_text">Thể Thao Điện Tử</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">101</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport2"></i><span className="btn_text">Bóng rổ</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">60</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport4"></i><span className="btn_text">Khúc côn cầu trên
                    băng</span><span className="badge">35</span></div>
                </div>
                <div className="side-nav_more">
                  <div className="btn btn-link"><span className="btn_text">Xem thêm (15)</span></div>
                </div>
              </div>
            </div>
            <div>
              <div className="side-nav_menu" data-status={isTab === 'parlay' ? 'is-open' : ''} data-more={isMoreSport ? 'is-open' : ''}>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport1"></i><span className="btn_text">Bóng đá</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">444</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport43"></i><span className="btn_text">Thể Thao Điện Tử</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">13</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport2"></i><span className="btn_text">Bóng rổ</span><span
                    className="badge">47</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport4"></i><span className="btn_text">Khúc côn cầu trên
                    băng</span><span className="badge">3</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport5"></i><span className="btn_text">Quần vợt</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">77</span>
                  </div>
                </div>
                <div className="side-nav_more">
                  <div className="btn btn-link"><span className="btn_text">Xem thêm (15)</span></div>
                </div>
              </div>
            </div>
            <div>
              <div className="side-nav_menu" data-status="" data-more="">
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport251"></i><span className="btn_text">Sòng bạc</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport245"></i><span className="btn_text">Trò chơi Ảo</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport200"></i><span className="btn_text">Cổng game Saba</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport170"></i><span className="btn_text">PP Live Casino</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport239"></i><span className="btn_text">BG</span></div>
                </div>
                <div className="side-nav_more">
                  <div className="btn btn-link"><span className="btn_text">Xem thêm (6)</span></div>
                </div>
              </div>
            </div>
            <div>
              <div className="side-nav_menu" data-status="" data-more="is-open">
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport161-3"></i><span className="btn_text">Tất Cả Trò Chơi Số</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">3</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport161"></i><span className="btn_text">Number Game</span>
                    <div className="label label-live">Trực tiếp</div>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport161-1"></i><span className="btn_text">Turbo Number Game</span>
                    <div className="label label-live">Trực tiếp</div>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport161-2"></i><span className="btn_text">Speed Number Game</span>
                    <div className="label label-live">Trực tiếp</div>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport164"></i><span className="btn_text">Happy 5</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">1</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport168-2"></i><span className="btn_text">Soccer 5</span>
                    <div className="label label-live">Trực tiếp</div>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport168-1"></i><span className="btn_text">Parlay 5</span>
                    <div className="label label-live">Trực tiếp</div><span className="badge">1</span>
                  </div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport202"></i><span className="btn_text">RNG Keno</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport169"></i><span className="btn_text">Xổ số Bóng đá</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport169-3"></i><span className="btn_text">Xổ Số NBA</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport169-2"></i><span className="btn_text">Xổ Số Thể Thao Điện
                    Tử</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport237"></i><span className="btn_text">MaxGame</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport200"></i><span className="btn_text">Keno Pro Max</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport220"></i><span className="btn_text">Xổ số</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport222"></i><span className="btn_text">Trò chơi trên bàn</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport224"></i><span className="btn_text">Funky Games</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport165-1"></i><span className="btn_text">Lottoworld</span></div>
                </div>
                <div className="side-nav_item">
                  <div className="btn"><i className="icon icon-sport213"></i><span className="btn_text">Lô Đề</span></div>
                </div>
                <div className="side-nav_item has-submenu" data-status="is-open">
                  <div className="btn"><i className="icon icon-sport18X"></i><span className="btn_text">Thể Thao Ảo</span><i
                    className="icon icon-arrow-bottom"></i></div>
                  <div className="side-nav_submenu">
                    <div className="side-nav_item">
                      <div className="btn"><i className="icon icon-sport231"></i><span className="btn_text">LG Virtual Sports </span>
                      </div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport197"></i><span className="btn_text">Cúp bóng đá Euro</span>
                      </div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport196"></i><span className="btn_text">Cúp bóng đá</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport194"></i><span className="btn_text">Cúp Châu Á</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport190"></i><span className="btn_text">Giải bóng đá</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport192"></i><span className="btn_text">Cúp thế giới</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport191"></i><span className="btn_text">Cúp quốc gia</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport180"></i><span className="btn_text">Bóng Đá</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport193"></i><span className="btn_text">Bóng rổ</span></div>
                    </div>
                    <div className="side-nav_subitem">
                      <div className="btn"><i className="icon icon-sport186"></i><span className="btn_text">Quần Vợt</span></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
}

export default LeftMenu;