/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import Login from "./Login";
import '../../../Assets/css/lobby.css';
import '../../../Assets/css/global-components.css';
import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/css';
import { useState } from "react";
import { useNavigate } from "react-router-dom";

export function AuthPage() {
  const [ login, setLogin ] = useState(false);
const navigate = useNavigate();
  const openLogin = () => { setLogin(!login); };
  const header = () => {
    return <div className="header">
      <div className="header_main">
        <div className="header_logo"></div>
        <button className="btn btn-secondary">Tham gia ngay</button>
        <button className="btn btn-highlight" onClick={openLogin}>Đăng nhập</button>
      </div>
      <div className="header_menu">
        <div className="header_scroll">
          <div id="topMenu" className="header_btns">
            <div id="topMenu0" className="btn">Thể Thao</div>
            <div id="topMenu1" className="btn">Thể Thao Ảo</div>
            <div id="topMenu2" className="btn">Thể thao điện tử</div>
            <div id="topMenu3" className="btn">Trò chơi Quay số</div>
            <div id="topMenu4" className="btn">Trò Chơi</div>
            <div id="topMenu5" className="btn">RNG Keno</div>
          </div>
        </div>

      </div>
    </div>;
  }

  const promo = () => {
    return <div className="promo"><Swiper
      spaceBetween={50}
      slidesPerView={1}
      onSlideChange={() => { }}
      onSwiper={(swiper) => { }}
    >
      <SwiperSlide>
        <img className="swiper-lazy swiper-lazy-loaded" src="https://mbi.vixcdn.com/public/beforeBanner/vn/Images/Banners/BBanner_VIVA88_MOBILE_298.jpg?v=20220210094444" />
      </SwiperSlide>
      <SwiperSlide>
        <img className="swiper-lazy swiper-lazy-loaded" src="https://mbi.vixcdn.com/public/beforeBanner/vn/Images/Banners/BBanner_VIVA88_MOBILE_299.jpg?v=20220210094444" />
      </SwiperSlide>
      <SwiperSlide>
        <img className="swiper-lazy swiper-lazy-loaded" src="https://mbi.vixcdn.com/public/beforeBanner/vn/Images/Banners/BBanner_VIVA88_MOBILE_296.jpg?v=20220210094444" />
      </SwiperSlide>
      <SwiperSlide>
        <img className="swiper-lazy swiper-lazy-loaded" src="https://mbi.vixcdn.com/public/beforeBanner/vn/Images/Banners/BBanner_VIVA88_MOBILE_278.jpg?v=20220210094444" />
      </SwiperSlide>
      <SwiperSlide>
        <img className="swiper-lazy swiper-lazy-loaded" id="slide5" src="https://mbi.vixcdn.com/public/beforeBanner/vn/Images/Banners/BBanner_VIVA88_MOBILE_140.jpg?v=20220210094444" />
      </SwiperSlide>
    </Swiper></div>
  }

  const changeLayout = () => {
    let url: any = window.location.hostname.split('m.');
    window.location.href = (`https://${url[1]}?layout=desktop`);
  }
  const content = () => {
    return <div id="lobby" className="content">
      {promo()}
      <div className="login_saba-entrance">
        <a className="btn btn-saba-entrance">
          <i className="icon-lobby icon-saba"></i>
          <div id="sabaText" className="text">Tỷ số Saba</div>
          <i className="icon-lobby icon-arrow-right"></i>
        </a>
      </div>
      <div id="productIcons" className="product">
        <div id="pItem0" className="product_item">
          <div id="pImg0" className="product_img soccer"></div>
          <div className="product_caption">
            <div id="pName0" className="product_name">Thể Thao</div>
            <div id="pIntro0" className="product_intro">Đặt cược và đắm mình trong môn thể thao phổ biến nhất thế giới</div>
          </div>
        </div>
        <div id="pItem1" className="product_item">
          <div id="pImg1" className="product_img esports"></div>
          <div className="product_caption">
            <div id="pName1" className="product_name">Thể thao điện tử</div>
            <div id="pIntro1" className="product_intro">Đặt cược vào các đội yêu thích trong các giải đấu trực tuyến hàng đầu</div>
          </div>
        </div>
        <div id="pItem2" className="product_item">
          <div id="pImg2" className="product_img numbergame"></div>
          <div className="product_caption">
            <div id="pName2" className="product_name">Trò chơi Quay số</div>
            <div id="pIntro2" className="product_intro">Đặt cược vào những con số may mắn</div>
          </div>
        </div>
        <div id="pItem3" className="product_item">
          <div id="pImg3" className="product_img basketball"></div>
          <div className="product_caption">
            <div id="pName3" className="product_name">Bóng rổ</div>
            <div id="pIntro3" className="product_intro">Những cú Swish hoàn hảo</div>
          </div>
        </div>
        <div id="pItem4" className="product_item">
          <div id="pImg4" className="product_img sabaclub"></div>
          <div className="product_caption">
            <div id="pName4" className="product_name">Saba.club</div>
            <div id="pIntro4" className="product_intro">Cổng game Việt cho người Việt!</div>
          </div>
        </div>
        <div id="pItem5" className="product_item">
          <div id="pImg5" className="product_img virtuals"></div>
          <div className="product_caption">
            <div id="pName5" className="product_name">Thể Thao Ảo</div>
            <div id="pIntro5" className="product_intro">Tận hưởng các trận đấu trực tuyến mà không phải chờ đợi</div>
          </div>
        </div>
        <div id="pItem6" className="product_item">
          <div id="pImg6" className="product_img gaming"></div>
          <div className="product_caption">
            <div id="pName6" className="product_name">Trò Chơi</div>
            <div id="pIntro6" className="product_intro">Slot, card và table game cùng rất nhiều lựa chọn</div>
          </div>
        </div>
        <div id="pItem7" className="product_item">
          <div id="pImg7" className="product_img keno"></div>
          <div className="product_caption">
            <div id="pName7" className="product_name">RNG Keno</div>
            <div id="pIntro7" className="product_intro">10+ RNG Keno games</div>
          </div>
        </div>
      </div>
    </div>

  }
  const footer = () => {
    return <div className="footer-lobby">
      <div className="footer_item">
        <div id="plName" className="footer_title">Nền Tảng</div>
        <div className="footer_content">
          <div id="desktop" className="btn" onClick={() => changeLayout()}>Máy tính để bàn</div>
          <div id="downloadApp" className="btn">Tải ứng dụng</div>
        </div>
      </div>
      <div className="footer_item">
        <div id="lanName" className="footer_title">Ngôn Ngữ</div>
        <div className="footer_content">
          <div className="btn">Tiếng Việt</div>
          <div className="btn">English</div>
          <div className="btn">繁體中文</div>
        </div>
      </div>
      <div id="footerP" className="footer_copy">© Copyright 2022. viva88 All Rights Reserved.</div>
    </div>
  }
  return (<>
    <div className="main login">
      {header()}
      {content()}
      {footer()}
    </div>
    <div className="modal-backdrop backdrop-beforelogin" style={{display: login ? 'block' : 'none'}} onClick={openLogin}></div>
    <Login show={login}/>
  </>
  )
}
