import { useEffect, useState } from "react";
import { useSelector, shallowEqual } from "react-redux";
import Done from "./Done";
import Result from "./Result";
import Running from "./Runing";
import { getResults, getStatement, getStatementDetails } from "./statementCrud";
import TabHeader from "./tabHeader";
import moment from "moment-timezone";
/* eslint-disable jsx-a11y/anchor-is-valid */
const Statement = (props: any) => {
  // moment.tz.setDefault('America/New_York'); 
  const { user } = useSelector(
    (state: any) => ({ user: state.auth }),
    shallowEqual
  );
  const { setSelected } = props;
  const [loading, setLoading] = useState(true);
  const [tabActive, setTabActive] = useState('runing');
  const [loadingFirst, setLoadingFirst] = useState(false);
  const [interval, setIntervalL] = useState(null as any);
  const [tabDone, setTabDone] = useState('today')
  const [tabResult, setTabResult] = useState('today');
  const [dayDiff, setDayDiff] = useState('Ngày khác')
  const [bets, setBets] = useState({
    data: {
      bets: [],
      betsInfo: null
    }
  } as any);

  const updateData = (tabActive: string, tab: string) => {

    tabActive === 'runing' && setIntervalL(setInterval(() => {
      getStatementDetails().then(({ data }: any) => {
        setBets(data);
      })
    }, 1000 * 10))
    tabActive === 'done' && setIntervalL(setInterval(() => {
      getStatement(tab).then(({ data }: any) => {
        setBets(data);
      })
    }, 1000 * 10))


    tabActive === 'result' && setIntervalL(setInterval(() => {
      getResults(tab).then(({ data }: any) => {
        setBets(data);
      })
    }, 1000 * 60))

  }
  useEffect(() => {
    setLoading(true);

    setBets({
      data: {
        bets: [],
        betsInfo: null
      }
    } as any);
    fetch(tabActive, tabDone, tabResult);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tabActive, tabDone, tabResult])


  const fetch = (tabActive: string, tabDone: string, tabResult: string) => {
    clearInterval(interval);
    tabActive === 'runing' && getStatementDetails().then(({ data }: any) => {
      setBets(data);
      setLoading(false);
      updateData(tabActive, tabDone);
      if (!loadingFirst) {
        if (data.data.bets.length > 0) {
          setTabActive('runing');
        } else {
          setTabActive('done');
        }
      }
      setLoadingFirst(true);
    })

    tabActive === 'done' && getStatement(tabDone).then(({ data }: any) => {
      setBets(data);
      setLoading(false);
      updateData(tabActive, tabDone);
    })

    if (tabActive === 'result') {
      let filter = tabResult === 'dayDiff' ? `${tabResult}&date=${moment(dayDiff).format('YYYY-MM-DD')}` : tabResult;
      getResults(filter).then(({ data }: any) => {
        setBets(data);
        setLoading(false);
        updateData(tabActive, tabResult);
      })
    }
  }

  return <>
    <div id="overlayComponents">
      <div id="pagePanel" className="contactUsPanel" data-status="is-open">
        <div>
          <div className="page page-result has-app-bar" id="MyBetspage_ch" data-status="is-open">
            <div className="comquickBet_sidebar">
              <div className="btn btn-watchbet-close"><i className="icon icon-clear"></i></div>
            </div>
            <div className="page_header">
              <div className="header_status">
                <div className="text-balance">UT {Number(user.wallet).toFixed(2)}</div>
              </div>
              <TabHeader tabActive={tabActive} setTabActive={setTabActive} />
            </div>
            <div className="page_content" id="MyBetsContentDiv_ch">
              {tabActive === 'runing' && <Running loading={loading} bets={bets} setSelected={setSelected} />}
              {tabActive === 'done' && <Done loading={loading} bets={bets} tabDone={tabDone} setTabDone={setTabDone} setSelected={setSelected} />}
              {tabActive === 'result' && <Result loading={loading} bets={bets} tabResult={tabResult} setTabResult={setTabResult} dayDiff={dayDiff} setDayDiff={setDayDiff} />}
            </div>
            <div className="gotoTop gotoTop-page"><i className="icon icon-arrow-top-end"></i></div>
          </div>
        </div>
      </div>
    </div>
  </>
}

export default Statement;