import { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import Loading from "../App/Loading";
import { getUserByToken } from "../Auth/_redux/authCrud";
import { actions } from "../Auth/_redux/authRedux";
import AppBar from "./AppBar";
import SportsHeader from "./Header";
import LeftMenu from "./leftMenu";
import Odds from "./odds";
import { getInPlayLeagues, getUpcomingPlayLeagues } from "./sportCrud";
import '../../Assets/css/main-lite-core.css'
import { Parlay } from "./parLay";
import { useLocation } from "react-router-dom";

const Sports = () => {
  const { search } = useLocation();

  const [loading, setLoading] = useState(false);
  const [openMenu, setOpentMenu] = useState(false);
  const [gameType, setGameType] = useState('');
  const [inPlayLeagues, setInPlayLeagues] = useState([] as any);
  const [upComingPlayLeagues, setUpComingPlayLeagues] = useState([] as any);
  const dispatch = useDispatch();
  const [totalLive, setTotalLive] = useState(0);
  const [totalToday, setTotalToday] = useState(0);
  const [tab, setTab] = useState('live');
  const [parlay, setParlay] = useState(false);
  const [parlayData, setParlayData] = useState([]);
  const [showParlay, setShowParlay] = useState(false);
  const [appBarSelect, setAppBarSelect] = useState('sports');
  const [intervalEvent, setIntervalEvent] = useState(0 as any);
  useEffect(() => {
    let type = search.split('?type=')[1];
    if (type !== gameType) {
      setGameType(type);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search])
  const updateData = () => {
    getInPlayLeagues(gameType).then(data => {
      let ttl = 0;
      data.data.data.forEach((l: any) => {
        ttl = ttl + l.events.length;
      })
      setTotalLive(ttl)
      setInPlayLeagues(data.data.data)
    })
    getUpcomingPlayLeagues(gameType).then(data => {
      setUpComingPlayLeagues(data.data.data)
      let ttl = 0;
      data.data.data.forEach((l: any) => {
        ttl = ttl + l.events.length;
      })
      setTotalToday(ttl)
    })

  };

  useEffect(() => {
    const node = document.createAttribute("data-core-lobby");
    node.value = 'false';
    document.documentElement.attributes.setNamedItem(node);
    const nodeAuth = document.createAttribute("data-auth");
    nodeAuth.value = 'false';
    document.documentElement.attributes.setNamedItem(nodeAuth);

    if (!loading) {
      loadEvent()
    }
    return () => clearInterval(intervalEvent);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loading, tab, gameType])

  useEffect(() => {

    if (intervalEvent) {
      setInPlayLeagues([]);
      loadEvent()
    }
    return () => clearInterval(intervalEvent);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [gameType])

  const loadEvent = () => {
    if (gameType !== '') {
      getUserByToken().then(({ data }) => {
        dispatch(actions.fulfillUser(data));
        getInPlayLeagues(gameType).then(data => {
          setInPlayLeagues(data.data.data)
          let ttl = 0;
          data.data.data.forEach((l: any) => {
            ttl = ttl + l.events.length;
          })
          setTotalLive(ttl)

          getUpcomingPlayLeagues(gameType).then(data => {
            setUpComingPlayLeagues(data.data.data)
            let ttl = 0;
            data.data.data.forEach((l: any) => {
              ttl = ttl + l.events.length;
            })
            setTotalToday(ttl)

          })
          setLoading(true)
          let id: any = setInterval(updateData, 10000)
          setIntervalEvent(id);

        })

      })
    }
  }
  useEffect(() => {
    console.log('chane appbar');
  }, [appBarSelect])

  return <>
    <Loading done={loading} />
    {loading && <div className="main">
      <AppBar selected={appBarSelect} />
      <SportsHeader openMenu={openMenu} setOpentMenu={setOpentMenu} totalLive={totalLive} totalToday={totalToday} tab={tab} setTab={setTab} parlay={parlay} setParlay={setParlay} gameType={gameType} setGameType={setGameType} intervalEvent={intervalEvent} />
      <LeftMenu openMenu={openMenu} setOpentMenu={setOpentMenu} />
      {tab === 'live' && <Odds data={inPlayLeagues} tab={tab} parlay={parlay} setParlayData={setParlayData} parlayData={parlayData} setAppBarSelect={setAppBarSelect} gameType={gameType}/>}
      {tab === 'today' && <Odds data={upComingPlayLeagues} live={inPlayLeagues} tab={tab} parlay={parlay} setParlayData={setParlayData} parlayData={parlayData} setAppBarSelect={setAppBarSelect} gameType={gameType}/>}

      {parlayData.length > 0 && <><div className="float-layer">
        <div className="float-icon-parlay" data-show="true">
          <div className="btn" onClick={() => setShowParlay(!showParlay)}><i className="icon icon-parlay"></i>
            <div className="badge badge-count">{parlayData.length}</div>
            <div className="tooltips tooltips-top tooltips-right tooltips--base-highlight">
              <div className="tooltips_arrow"></div>
              <div className="tooltips_content"><span className="text"></span></div>
            </div>
          </div>
        </div>
      </div>
      </>}
      <Parlay data={upComingPlayLeagues} live={inPlayLeagues} showParlay={showParlay} parlayData={parlayData} setShowParlay={setShowParlay} setParlayData={setParlayData} setAppBarSelect={setAppBarSelect} />
    </div>
    }
  </>
}

export default Sports;