import moment from "moment-timezone";
import { useEffect, useState } from "react";
import { getStatementDetailsDone } from "../statementCrud";
const BetDetail = (props: any) => {
  const { bet, setSelected, foldOpen, setLoadingFold, loadingFold } = props;
  const [openDetail, setOpentDetail] = useState(false);
  const [bets, setBets] = useState(null as any)

  useEffect(() => {
    if (foldOpen) {
      setLoadingFold(true);
      getStatementDetailsDone(moment(bet.date).format('YYYY-MM-DD').toString()).then(({ data }: any) => {
        setBets(data.data.bets);
        setLoadingFold(false);
      })
    } else {
      setBets(null)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [foldOpen])

  const betNormal = (bet: any) => {
    return <>{bet && <div className="bets bets-running" style={{ display: 'block' }} data-status={openDetail ? 'is-open' : ''}>
      <div className="bets_header">
        <div className="bets_title">
          <span className="">{bet.bet_name}</span>
          <span className="text-odds">
            {bet.bet_odd}
            @{bet.bet_value}
          </span>
        </div>

        <div className="share_btn_zone ticket-114824929253" style={{ width: '100%' }}></div>
        <div className="lc-btn lc-btn-chat"><i className="lc-icon lc-icon--send"></i></div>
      </div>

      <div className="bets_content touch">
        <div className="text-lowlight">Bóng đá /Trực tiếp {bet.bet_type} / {bet.ss}</div>
        <div className="text-lowlight">
          
          <span>{bet.home}</span> vs <span>{bet.away}</span>
        </div>
        <div className="text-lowlight"></div>
        <div className="text-lowlight"></div>

        <div className="text-lowlight">Thời gian cược : {moment(bet.date).format('DD/MM/YYYY h:m A').toString()}</div>
        <div className="text-lowlight">Kết quả : {bet.last_ss}</div>

        <div className="bets_total">
          <div className="info">
            <div className="info">
              <div className="info_title">Tiền cược:</div>
              <div className="info_value">{bet.bet_amount}</div>
            </div>
          </div>
          <div className="info">
            <div className="info_title">{bet.bet_status_name}
              {bet.bet_status !== 'draw' && <>:</>}
            </div>
            {bet.bet_status !== 'draw' && <div className="info_value"><span id="Status_114824929253" className={`text-${bet.bet_status === 'lose' ? 'loss' : 'win'}`}>{bet.bet_profit}</span></div>}
          </div>
          <i className="icon icon-arrow-bottom" onClick={() => setOpentDetail(!openDetail)}></i>
        </div>
      </div>
      <div className="bets_footer">
        <div className="text-lowlight">{bet.league_name}</div>
        <div className="text-lowlight">Thời gian Sự kiện : {moment(bet.date).format('DD/MM/YYYY h:m A').toString()}</div>
        <div className="text-lowlight">Loại tỷ lệ cược: MY</div>
        <div className="text-lowlight">ID: {bet.id}</div>
        <div className="bets_info">
          <div className="text-ip">
            <span className="c-text">
              {bet.ip_address}
            </span>
            <a className="c-btn">
              <i className="c-icon c-icon--copy"></i>
            </a>
          </div>
        </div>
        <span className="c-text-code"></span>
      </div>
      <div id="CashOut_114824929253" className="c-cashout-ticket-container">
        <div className="c-cashout-ticket" data-open="false" style={{ display: 'block' }}>
          <div className="c-cashout-collapse" data-disabled="true">
            <i className="c-icon c-icon--cashout"></i>
            <span className="c-text">Hiện không thể Bán Vé Cược</span>
          </div>
          <div className="c-cashout">
            <div className="c-cashout-sell">
              <span className="c-cashout-sell__item">
                <span className="c-text-label">Tiền cược</span><span className="c-text-value">{bet.bet_amount}</span></span><span
                  className="c-cashout-sell__item"><span className="c-text-label">Bán Vé Cược</span>
                <div className="c-btn c-btn--howtouse"><i className="c-icon c-icon--help-outline"></i></div><span
                  className="c-text-value">0.00</span>
              </span>
            </div>
            <div className="c-cashout-submit" data-disabled="false">
              <div className="c-btn c-btn--primary"><i className="c-icon c-icon--refresh"></i></div>
            </div>

          </div>
        </div>
      </div>
      <div className="bets_option">
        <div className="btn btn-link" onClick={() => setSelected('sports')}>
          <i className="icon icon-return"></i>
          <div className="btn_text">Quay lại Sự Kiện </div>
        </div>
      </div>
    </div>}</>
  }

  const betGroup = (bet: any) => {
    return <div className="bets bets-running" style={{ display: 'block' }} data-status={openDetail ? 'is-open' : ''}>
      <div className="bets_header">
        <div className="bets_group">
          <div className="bets_title">
            Cược {bet.bets.length} (1 Đặt cược)
            <span> @ {bet.rate}</span>
          </div>
          <div className="bets_stake">{bet.bet_amount}</div>
        </div>

        <div className="share_btn_zone ticket-114849923886" style={{ width: '100%' }}></div>
        <div className="lc-btn lc-btn-chat"><i className="lc-icon lc-icon--send"></i></div>
      </div>
      <div className="bets_content touch">
        {bet.bets.map((bet: any, idx: number) => {
          return <div key={idx}><div className="text-lowlight bets-running">{bet.bet_name} {bet.bet_odd}<span>@{bet.bet_value}</span></div></div>
        })}

        <div className="text-lowlight">Thời gian cược : {moment(bet.date).format('YYYY-MM-DD').toString()}</div>

        <div className="bets_total">
          <div className="info">
            <div className="info">
              <div className="info_title">Tiền cược:</div>
              <div className="info_value">{bet.bet_amount}</div>
            </div>
          </div>
          <div className="info">
            <div className="info_title">{bet.bet_status_name}</div>
            {bet.bet_status !== 'draw' && <>:</>}
            <div className="info_value"><span id="Status_114849923886" className={`text-${bet.bet_status === 'lose' ? 'loss' : 'win'}`}>{bet.bet_profit}</span></div>
          </div>
          <i className="icon icon-arrow-bottom" onClick={() => setOpentDetail(!openDetail)}></i>
        </div>
      </div>

      {bet.bets.map((bet: any, idx: any) => {
        return <div className="bets_footer bets_footer-parlay  bets-running" key={idx}>
          <div className="text-lowlight">{bet.bet_name} {bet.bet_odd}<span>@{bet.bet_value}</span></div>
          <div className="text-lowlight">
            [{bet.ss}]
            <span>{bet.home}</span> vs <span>{bet.away}</span>
          </div>
          <div className="text-lowlight">Bóng đá /Trực tiếp {bet.bet_type}</div>
          <div className="text-lowlight"></div>
          <div className="text-lowlight"></div>
          <div className="text-lowlight">{bet.bet_name}</div>
          <div className="bets_info text-lowlight">
            <div className="text-lowlight">{moment(bet.date).format('YYYY-MM-DD').toString()}</div>
            <div className="info">
              <div className="info_title">Trạng thái:</div>
              <div className="info_value"><span className="text-default">{bet.bet_status_name}</span></div>
            </div>
          </div>
          <span className="c-text-code"></span>
        </div>
      })}
    </div>
  }
  
  
  return <>{foldOpen && <>
    {bets && bets.map((bet: any, idx: number) => {
      return <div key={idx}>
        {bet.bet_kind === 'normal' && betNormal(bet)}
        {bet.bet_kind === 'group' && betGroup(bet)}
      </div>
    })}

  </>}</>;
}

export default BetDetail;