import { useEffect, useState } from "react";
import { useSelector, shallowEqual } from "react-redux";
import { ConFirmBet } from "./ConfirmBet";
import { createBet } from "./sportCrud";

const Bet = (props: any) => {
  const { stateApp } = useSelector(
    (state: any) => ({ stateApp: state }),
    shallowEqual
  );
  const { user } = stateApp.auth;

  const { leagues, setBet, bet, parlayData, setParlayData, setAppBarSelect } = props;
  const [betValue, setBetValue] = useState(0)
  const [betType, setBetType] = useState('')
  const [betChoice, setBetChoice] = useState('')
  const [betGoal, setBetGoal] = useState('');
  const [betBalance, setBetBalance] = useState([] as string[]);
  const [betBalanceFormat, setBetBalanceFormat] = useState(0);
  const [payAmount, setPaytAmount] = useState(0)
  const [openConfirm, setOpenConfirm] = useState(false);
  const [msg, setMsg] = useState('');
  const [process, setProcess] = useState('process');
  const [betSuccess, setBetSuccess] = useState(null);
  useEffect(() => {
    let betB = betBalance.toString();
    betB = betB.replaceAll(',', '');
    setBetBalanceFormat(Number(betB));

    setPaytAmount(Number(betB) + (Number(betB) * Math.abs(Number(betValue))));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [betBalance])
  const [event, setEvent] = useState(null as any);
  const [odd, setOdd] = useState(null as any);

  const handleSubmit = () => {
    if (betBalanceFormat === 0) {
      setMsg('Vui lòng nhập mức cược!');
    } else if (betBalanceFormat > Number(user.bongdamax)) {
      setMsg('Mức cược của bạn lớn hơn hạn mức cược tối đa!');
    } else if (betBalanceFormat > Number(user.wallet)) {
      setMsg('Mức cược của bạn lớn hơn hạn mức cược !');
    } else {

      let betT = bet.betType.split('.');
      let type = '';
      if (betT[1] === 'home_od' || betT[1] === 'over_od') {
        type = 'home'
      }
      if (betT[1] === 'away_od' || betT[1] === 'under_od') {
        type = 'away'
      }

      if (betT[1] === 'draw_od') {
        type = 'draw'
      }
      const betInfo = {
        bet_amount: betBalanceFormat,
        odd_id: odd.odd_id.toString(),
        bet_value: betValue,
        bet_position: type === 'home' ? 0 : type === 'away' ? 1 : 2,
        type: type,//away
        bet_type: betT[0]
      }

      createBet(betInfo).then(({ data }) => {
        if (data.code === 'SUCCESS') {
          setOpenConfirm(true);
          setBetSuccess(data.data);
        } else {
          alert(data.msg);
        }
      }).catch(err => {
        console.log(err);
      })
    }

    // createBet
  }

  const addParlay = () => {
    if (odd?.is_parlay) {
      let prPl = parlayData.findIndex((p: any) => p.eventId === bet.eventId);
      if (prPl > -1) {
        if (parlayData[prPl].betType === bet && parlayData[prPl].oddId === bet.oddId) {
          //Remove
          setParlayData(parlayData.filter((p: any) => p.eventId !== bet.eventId));
        } else {
          //update
          parlayData[prPl] = { ...parlayData[prPl], betType: bet, oddId: bet.oddId }
          setParlayData([...parlayData]);
        }

      } else {
        setParlayData([...parlayData, bet]);
      }
    }
  }
  const handicapValue =(odd: any) => {
    let betT = bet.betType.split('.');
    let bet_position = -1;
    if (betT[1] === 'home_od' || betT[1] === 'over_od') {
      bet_position = 0
    }
    if (betT[1] === 'away_od' || betT[1] === 'under_od') {
      bet_position = 1
    }

    if(bet.gameType === 'saba') {
      return (bet_position === 0 ? -1: 1) * parseFloat(odd.handicap_value);
    }
    return ((odd.handicap_team === 'home' ? -1 : 1) * (bet_position === 0 ? 1: -1)) * parseFloat(odd.handicap_value);
  }
  useEffect(() => {
    if (leagues) {
      const league = leagues.find((l: any) => l.league_id === bet.leagueId);
      if (league) {
        let _event = league.events.find((e: any) => e.event_id === bet.eventId);
        if (_event) {
          setEvent(_event);
          let _odd = _event.odds.find((o: any) => o.odd_id === bet.oddId);
          if (_odd) {
            setOdd(_odd);
            const betV = bet.betType.split('.');
            setBetValue(_odd[betV[0]][betV[1]]);
            if (bet.betType === 'ft_hdp.home_od' || bet.betType === 'ft_hdp.away_od' || bet.betType === 'hf_hdp.home_od' || bet.betType === 'hf_hdp.away_od') {
              if(bet.betType.lastIndexOf('ft_hdp',0) === 0) {
              setBetType('Trực tiếp Cược Chấp');
              } else if(bet.betType.lastIndexOf('hf_hdp',0) === 0) {
                setBetType('Trực tiếp Cược Chấp / Hiệp 1');
              }
              if (bet.betType === 'ft_hdp.home_od' || bet.betType === 'hf_hdp.home_od') {
                setBetChoice(_event.home)
              }
              if (bet.betType === 'ft_hdp.away_od' || bet.betType === 'hf_hdp.away_od') {
                setBetChoice(_event.away)
              }
              if (betV[0] === 'ft_hdp') {
                setBetGoal(handicapValue(_odd.ft_hdp).toString());
              }

              if (betV[0] === 'hf_hdp') {
                setBetGoal(handicapValue(_odd.hf_hdp).toString());
              }
            }

            if (bet.betType === 'ft_ou.over_od' || bet.betType === 'ft_ou.under_od' || bet.betType === 'hf_ou.over_od' || bet.betType === 'hf_ou.under_od') {
             
              if(bet.betType.lastIndexOf('ft_ou',0) === 0) {
                setBetType('Trực tiếp Tài/Xỉu');
                } else if(bet.betType.lastIndexOf('hf_ou',0) === 0) {
                  setBetType('Trực tiếp Tài/Xỉu / Hiệp 1');

                }
              if (bet.betType === 'ft_ou.over_od' || bet.betType === 'hf_ou.over_od') {
                setBetChoice('Tài')
              }
              if (bet.betType === 'ft_ou.under_od' || bet.betType === 'hf_ou.under_od') {
                setBetChoice('Xỉu')
              }

              if (betV[0] === 'ft_ou') {
                setBetGoal(_odd.ft_ou.handicap);
              }

              if (betV[0] === 'hf_ou') {
                setBetGoal(_odd.hf_ou.handicap);
              }
            }

            if (bet.betType === 'ft_1x2.home_od' || bet.betType === 'ft_1x2.away_od' || bet.betType === 'ft_1x2.draw_od' || bet.betType === 'hf_1x2.home_od' || bet.betType === 'hf_1x2.away_od' || bet.betType === 'hf_1x2.draw_od') {
              if(bet.betType.lastIndexOf('ft_1x2',0) === 0) {
                setBetType('Trực tiếp FT.1X2');
                } else if(bet.betType.lastIndexOf('hf_1x2',0) === 0) {
                  setBetType('Trực tiếp FT.1X2 / Hiệp 1');

                }
              
              if (bet.betType === 'ft_1x2.home_od' || bet.betType === 'hf_1x2.home_od') {
                setBetChoice(_event.home)
              }
              if (bet.betType === 'ft_1x2.away_od' || bet.betType === 'hf_1x2.away_od') {
                setBetChoice(_event.away)
              }

              if (bet.betType === 'ft_1x2.draw_od' || bet.betType === 'hf_1x2.draw_od') {
                setBetChoice('Hòa')
              }
            }
          }
        }
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [leagues])

  return <>
    <div id="quickBet" >
      <div>
        <div id="comquickBet" className="comquickBet" data-status={bet ? 'is-active' : ''} data-center={openConfirm ? 'true' : 'false'} onClick={() => msg !== '' && setMsg('')}>
          <div className="overlay" data-status={bet ? 'is-open' : ''} onClick={() => setBet(null) && setOpenConfirm(false)}></div>
          <div className="comquickBet_sidebar">
            <div className="btn btn-watchbet-close"><i className="icon icon-clear"></i></div>
            <div className="btn btn-watchbet-return"><i className="icon icon-return"></i></div>
          </div>
          {!openConfirm && <div className="comquickBet_panel">
            <div className="comquickBet_container" data-status="is-open">
              <div className="comalert">
                <div className="comalert_text"></div>
              </div><i className="icon icon-clear" onClick={() => setBet(null)}></i>
              <div>
                <div className="comtickets ">
                  <div className="comtickets_head"><i className="icon icon-sport1"></i>
                    <div className="comtickets_league">{event?.league_name}</div>
                    <div className="comtickets_match">
                      <div className="comtickets_home">{event?.home}</div>
                      <div className="comtickets_score">{event?.ss}</div>
                      <div className="comtickets_away">{event?.away}</div>
                    </div>
                  </div>
                  <div className="comtickets_status">
                    <div className="comtickets_top" data-scroll="true">
                      <div className="comtickets_betType">Bóng đá / {betType}</div>
                      <div className="comtickets_team">
                        <div className="combetType_choice">{betChoice}</div>
                        <div className="combetType_goal">{betGoal}</div>
                      </div>
                    </div>
                    <div className="comtickets_bottom">
                      <div className={`comtickets_odds ${Number(betValue) < 0 ? 'betType-minusOdds' : ''}`}>{betValue}</div>
                      <div className="comodds_type">(MY)</div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="" data-status="">
                <div className="comtickets_bet">
                  <div className="comtickets_input" data-disabled="false">
                    <div id="tooltips" className="tooltips tooltips-bottom tooltips-warning" data-status={msg !== '' ? 'is-open' : ''}>
                      <div className="tooltips_arrow"></div>
                      <div id="tipContent" className="tooltips_content">{msg}</div>
                    </div>
                    <div className="textfield textfield-stake" data-focus="true" data-selected={msg !== '' ? 'true' : 'false'}>
                      <div className="textfield_currency">UT</div>
                      <div className="textfield_input">

                        {betBalanceFormat > 0 && <>{betBalanceFormat.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</>}
                        {betBalanceFormat === 0 && <div className="comtickets_info">
                          <div className="comtickets_value"> {user.bongdamin} - {user.bongdamax}</div>
                        </div>}
                      </div>
                      <div className="btn btn-cancel" onClick={() => setBetBalance([])}><i className="icon icon-cancel"></i></div>
                    </div>
                  </div>
                  <div className="comtickets_info">
                    <div className="comtickets_item">
                      <div className="comtickets_title">Hạn mức còn lại:</div>
                      <div className="comtickets_value">{Number(user.wallet).toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
                    </div>
                    <div className="comtickets_item">
                      <div className="comtickets_title">Thanh toán:</div>
                      <div className="comtickets_value">{Number(payAmount).toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
                    </div>
                  </div>
                </div>
                <div className="comkeypad" data-status="is-open">
                  <div className="comkeypad_left">
                    <div className="comkeypad_lefttop">
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '1'])}>1</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '2'])}>2</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '3'])}>3</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '4'])}>4</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '5'])}>5</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '6'])}>6</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '7'])}>7</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '8'])}>8</div>
                      <div className="comkeypad_item" onClick={() => setBetBalance([...betBalance, '9'])}>9</div>
                      <div className="comkeypad_item" onClick={() => betBalance.length > 0 && setBetBalance([...betBalance, '0'])}>0</div>
                    </div>
                    <div className="comkeypad_leftbottom">
                      <div className="comkeypad_item keypad-dark" onClick={() => {
                        const newBalance = betBalanceFormat + 6;
                        setBetBalanceFormat(Number(newBalance))
                        setBetBalance(Array.from(newBalance.toString()))
                      }}>+6</div>
                      <div className="comkeypad_item keypad-dark" onClick={() => {
                        const newBalance = betBalanceFormat + 15;
                        setBetBalanceFormat(Number(newBalance))
                        setBetBalance(Array.from(newBalance.toString()))
                      }}>+15</div>
                      <div className="comkeypad_item keypad-dark" onClick={() => {
                        const newBalance = betBalanceFormat + 30;
                        setBetBalanceFormat(Number(newBalance))
                        setBetBalance(Array.from(newBalance.toString()))
                      }}>+30</div>
                    </div>
                  </div>
                  <div className="comkeypad_right">
                    <div className="comkeypad_item" onClick={() => {
                      betBalance.pop();
                      setBetBalance([...betBalance]);
                    }}>
                      <span className="comkeypad_caculator">
                        <i className="icon icon-backspace"></i>
                      </span>
                    </div>
                    <div className="comkeypad_item" onClick={() => betBalance.length > 0 && setBetBalance([...betBalance, '00'])}>00</div>
                    <div className="comkeypad_item keypad-dark" onClick={() => {
                      // setBetBalance([]);
                      setBetBalanceFormat(Number(user.wallet))
                      setBetBalance(Array.from(user.wallet.toString()))
                    }}>Tối đa</div>
                  </div>
                </div>
                <div className="combetting">
                  <div className="lc-btn lc-btn-chat"><i className="lc-icon lc-icon--live-chat"></i></div>
                  <div className="combtn combtn--add-parlay" data-added="false" data-disabled={odd?.is_parlay ? 'disabled' : 'true'} onClick={() => addParlay()}>
                    <i className="icon icon-addition"></i><i className="icon icon-successful"></i><span className="text">Cược xiên</span>
                  </div>
                  <div id="comBtn" className="combtn btn-bet" onClick={() => {
                    handleSubmit();
                  }}>Đặt Cược</div>
                  {!odd?.is_parlay && <span className="text-highlight">*Cược xiên không được cung cấp cho loại cược này.</span>}
                  <div className="comalert">
                    <div className="comalert_text"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          }
          {openConfirm && <ConFirmBet setOpenConfirm={setOpenConfirm} process={process} betValue={betValue} payAmount={payAmount} betBalanceFormat={betBalanceFormat} goal={betGoal} event={event} betType={betType} betChoice={betChoice} setBet={setBet} betSuccess={betSuccess} setAppBarSelect={setAppBarSelect} />}
        </div>
      </div>
    </div >
  </>
}

export default Bet;