import Footer from "../Footer"
import Header from "../Header"
import Navigation from "../Navigations"
import '../../Assets/css/global-lobby-core.css';
import '../../Assets/css/global-lite-core.css';

import Banner from "./Banner";
import Loading from "./Loading";
import { getUserByToken } from "../Auth/_redux/authCrud";
import { useEffect, useState } from "react";
import { actions } from "../Auth/_redux/authRedux";
import { useDispatch } from "react-redux";
import SportsNav from "./sportsNav";
import ProductList from "./productList";
const Layout = () => {
  const [loading, setLoading] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    const node = document.createAttribute("data-core-lobby");
    node.value = 'true';
    document.documentElement.attributes.setNamedItem(node);
    const nodeAuth = document.createAttribute("data-auth");
    nodeAuth.value = 'false';
    document.documentElement.attributes.setNamedItem(nodeAuth);
    !loading && getUserByToken().then(({ data }) => {
      dispatch(actions.fulfillUser(data));
      setLoading(true)
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loading])
  return <><Loading done={loading} />
    {loading && <div>
      <div className="c-app" id="newlobbyRoot">
        <Header />
        <Banner />
        <Navigation />
        <div className="c-main">
          <div className="c-page__content">
            <div className="c-product-sports" data-selected="true">
              <SportsNav />
              <ProductList />
            </div>
          </div>
        </div>
        <Footer />
      </div>

    </div>}
  </>
}

export default Layout;