import BetRunning from "./BetRunning";

const Running = (props: any) => {
  const { bets, loading, setSelected } = props;

  const infoRuningHeader = () => {
    return <div className="info_header">
      <div className="page_infobar ">
        <div className="info">
          <div className="info_title">Mở {bets?.data.betsInfo?.count ?? 0} Đơn cược</div>
        </div>
        <div className="c-cashout-filter" data-disabled="true"><i className="c-icon c-icon--cashout"></i><span
          className="c-text">Xem Vé Được Bán</span>
          <div className="c-switch-btn" data-switch="off">
            <div className="c-switch">
              <div className="c-switch__dot"></div>
            </div>
          </div>
        </div>
      </div>
      <div className="page_totalbar">
        <div className="dropdown">
          <div className="btn" data-toggle="dropdown" aria-expanded="false">
            <div className="btn_text">Thể Thao</div>
          </div>
          <ul className="dropdown-menu" role="menu">
            <li className="active"><a>Thể Thao</a></li>
          </ul>
        </div>
        <div className="info">
          <div className="info_title">Tổng cược:</div>
          <div id="RunningTotalCount" className="info_value">{bets?.data.betsInfo?.stake ?? 0}</div>
        </div>
      </div>
    </div>
  }
  return <>
    {infoRuningHeader()}
    <BetRunning bets={bets} loading={loading} setSelected={setSelected} />
  </>
}

export default Running;