/* eslint-disable jsx-a11y/iframe-has-title */
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { getLive } from "../sportCrud";

/* eslint-disable jsx-a11y/anchor-is-valid */
export const Matchs = (props: any) => {
  const { match, tab, parlay, setParlayData, parlayData, gameType } = props;
  const [oddMatch, setOddMatch] = useState(null as any);
  const [openStream, setOpenStream] = useState(false);
  const [srcStream, setSourceStream] = useState('');
  const [loading, setLoading] = useState(true);
  const [fullScreen, setfullScreen] = useState(true);
  const [active, setActive] = useState(true);
  const [moreLines, setMoreLines] = useState(false);
  const [tabMoreLive, setTabMoreLive] = useState('ft_hdp');
  const [oddMores, setOddMores] = useState([] as any[])
  const navigate = useNavigate();
  const [commatch, setCommatch] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setOddMatch(match);
      let oddMores = [];
      for (var i = 1; i <= match.odds.length; i++) {
        oddMores.push(match.odds[i])
      }
      setOddMores(oddMores)
    }, 5000)

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [match])
  useEffect(() => {
    if (openStream) {
      getLive(match.event_id, match.live_id).then((data) => {
        setSourceStream(data.data.data);
        setLoading(false);
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [openStream])

  const convertMalayToDecimal = (value: number) => {
    if (parlay) {
      if (value < 0) {
        value = -1/value;
      }
      return Math.round((value + 1) * 100) / 100;
    }
    return value ?? 0;
  }

  const ftHdp = () => {
    const odd: any = match.odds[0];
    if (odd) {
      const selecteParlay: any = parlayData && parlayData.find((p: any) => p.oddId === odd.odd_id);
      let home = '';
      let away = '';
      let o = '';
      let u = '';
      let xh = '';
      let xa = '';
      let dr = '';
      if (oddMatch) {
        const oddOld = oddMatch.odds[0];
        if (oddOld !== undefined) {
          if (odd.ft_hdp.home_od !== oddOld.ft_hdp.home_od) {
            if (odd.ft_hdp.home_od > oddOld.ft_hdp.home_od) {
              home = 'up';
            } else {
              home = 'down'
            }
          }
          if (odd.ft_hdp.away_od !== oddOld.ft_hdp.away_od) {
            if (odd.ft_hdp.away_od > oddOld.ft_hdp.away_od) {
              away = 'up';
            } else {
              away = 'down'
            }
          }

          if (odd.ft_ou.over_od !== oddOld.ft_ou.over_od) {
            if (odd.ft_ou.over_od > oddOld.ft_ou.over_od) {
              o = 'up';
            } else {
              o = 'down'
            }
          }
          if (odd.ft_ou.under_od !== oddOld.ft_ou.under_od) {
            if (odd.ft_ou.under_od > oddOld.ft_ou.under_od) {
              u = 'up';
            } else {
              u = 'down'
            }
          }

          if (odd.ft_1x2.home_od !== oddOld.ft_1x2.home_od) {
            if (odd.ft_1x2.home_od > oddOld.ft_1x2.home_od) {
              xh = 'up';
            } else {
              xh = 'down'
            }
          }
          if (odd.ft_1x2.away_od !== oddOld.ft_1x2.away_od) {
            if (odd.ft_1x2.away_od > oddOld.ft_1x2.away_od) {
              xa = 'up';
            } else {
              xa = 'down'
            }
          }

          if (odd.ft_1x2.draw_od !== oddOld.ft_1x2.draw_od) {
            if (odd.ft_1x2.draw_od > oddOld.ft_1x2.draw_od) {
              dr = 'up';
            } else {
              dr = 'down'
            }
          }
        }

      }
      setTimeout(() => {
        home = '';
        away = '';
        u = '';
        o = '';
        xh = '';
        xa = '';
        dr = '';
      }, 2000)
   
      return <>
        <div className="bettype_group" data-more-lines="true">
          {match.odds.length > 1 && <div className="btn btn-more-lines" data-full-width="false" onClick={() => setMoreLines(!moreLines)}><span className="btn_text-line-2">Cược Châu Á khác</span></div>}
          <div className="bettype_item">
            <div className="bettype_name">Toàn Trận - Cược Chấp</div>
            {odd.ft_hdp.home_od && Number(odd.ft_hdp.home_od) !== 0 && <div className={`bettype_oddsbox ${home ? `comodds-${home}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_hdp.home_od') ? 'is-active' : ''} onClick={() => openBet('ft_hdp.home_od', odd.odd_id)}>
              <div className="oddsbox_goal">{odd?.ft_hdp.handicap_team === 'home' && odd.ft_hdp.handicap}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_hdp.home_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_hdp.home_od)).toFixed(2)}</div>
            </div>}
            {odd.ft_hdp.away_od && Number(odd.ft_hdp.away_od) !== 0 && <div className={`bettype_oddsbox ${away ? `comodds-${away}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_hdp.away_od') ? 'is-active' : ''} onClick={() => openBet('ft_hdp.away_od', odd.odd_id)}>
              <div className="oddsbox_goal">{odd.ft_hdp.handicap_team === 'away' && odd.ft_hdp.handicap}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_hdp.away_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_hdp.away_od)).toFixed(2)}</div>
            </div>
            }
            <div className="bettype_oddsbox"></div>
          </div>
          <div className="bettype_item">
            <div className="bettype_name">Toàn Trận - Tài/Xỉu</div>
            {odd.ft_ou.over_od && Number(odd.ft_ou.over_od) !== 0 && <div className={`bettype_oddsbox ${o ? `comodds-${o}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_ou.over_od') ? 'is-active' : ''} onClick={() => openBet('ft_ou.over_od', odd.odd_id)}>
              <div className="oddsbox_goal">o {odd.ft_ou.handicap}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_ou.over_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_ou.over_od)).toFixed(2)}</div>
            </div>}
            {odd.ft_ou.under_od && Number(odd.ft_ou.under_od) !== 0 && <div className={`bettype_oddsbox ${u ? `comodds-${u}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_ou.under_od') ? 'is-active' : ''} onClick={() => openBet('ft_ou.under_od', odd.odd_id)}>
              <div className="oddsbox_goal">{odd.ft_ou.under_od ? 'u' : ''}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_ou.under_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_ou.under_od)).toFixed(2)}</div>
            </div>}
            <div className="bettype_oddsbox"></div>
          </div>
          <div className="bettype_item">
            <div className="bettype_name">Toàn Trận - 1X2</div>
            {odd.ft_1x2.home_od && Number(odd.ft_1x2.home_od) !== 0 && <div className={`bettype_oddsbox ${xh ? `comodds-${xh}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_1x2.home_od') ? 'is-active' : ''} onClick={() => openBet('ft_1x2.home_od', odd.odd_id)}>
              <div className="oddsbox_goal">Đội nhà</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_1x2.home_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_1x2.home_od)).toFixed(2)}</div>
            </div>}
            {odd.ft_1x2.away_od && Number(odd.ft_1x2.away_od) !== 0 && <div className={`bettype_oddsbox ${xa ? `comodds-${xa}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_1x2.away_od') ? 'is-active' : ''} onClick={() => openBet('ft_1x2.away_od', odd.odd_id)}>
              <div className="oddsbox_goal">Đội khách</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_1x2.away_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_1x2.away_od)).toFixed(2)}</div>
            </div>}
            {odd.ft_1x2.draw_od && Number(odd.ft_1x2.draw_od) !== 0 && <div className={`bettype_oddsbox ${dr ? `comodds-${dr}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'ft_1x2.draw_od') ? 'is-active' : ''} onClick={() => openBet('ft_1x2.draw_od', odd.odd_id)}>
              <div className="oddsbox_goal">Hòa</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.ft_1x2.draw_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.ft_1x2.draw_od)).toFixed(2)}</div>
            </div>
            }
          </div>
        </div>
      </>
    }
  }
  const hf1x2 = () => {
    const odd = match.odds[0];
    if (odd) {
      const selecteParlay: any = parlayData && parlayData.find((p: any) => p.oddId === odd.odd_id);
      let home = '';
      let away = '';
      let o = '';
      let u = '';
      let xh = '';
      let xa = '';
      let dr = '';
      if (oddMatch) {
        const oddOld = oddMatch.odds[0];
        if (oddOld !== undefined) {
          if (odd.hf_hdp.home_od !== oddOld.hf_hdp.home_od) {
            if (odd.hf_hdp.home_od > oddOld.hf_hdp.home_od) {
              home = 'up';
            } else {
              home = 'down'
            }
          }
          if (odd.hf_hdp.away_od !== oddOld.hf_hdp.away_od) {
            if (odd.hf_hdp.away_od > oddOld.hf_hdp.away_od) {
              away = 'up';
            } else {
              away = 'down'
            }
          }

          if (odd.hf_ou.over_od !== oddOld.hf_ou.over_od) {
            if (odd.hf_ou.over_od > oddOld.hf_ou.over_od) {
              o = 'up';
            } else {
              o = 'down'
            }
          }
          if (odd.hf_ou.under_od !== oddOld.hf_ou.under_od) {
            if (odd.hf_ou.under_od > oddOld.hf_ou.under_od) {
              u = 'up';
            } else {
              u = 'down'
            }
          }

          if (odd.hf_1x2.home_od !== oddOld.hf_1x2.home_od) {
            if (odd.hf_1x2.home_od > oddOld.hf_1x2.home_od) {
              xh = 'up';
            } else {
              xh = 'down'
            }
          }
          if (odd.hf_1x2.away_od !== oddOld.hf_1x2.away_od) {
            if (odd.hf_1x2.away_od > oddOld.hf_1x2.away_od) {
              xa = 'up';
            } else {
              xa = 'down'
            }
          }

          if (odd.hf_1x2.draw_od !== oddOld.hf_1x2.draw_od) {
            if (odd.hf_1x2.draw_od > oddOld.hf_1x2.draw_od) {
              dr = 'up';
            } else {
              dr = 'down'
            }
          }

        }
      }
      setTimeout(() => {
        home = '';
        away = '';
        u = '';
        o = '';
        xh = '';
        xa = '';
        dr = '';
      }, 2000)

      const isShow = Number(odd.hf_hdp.home_od) !== 0 || Number(odd.hf_hdp.away_od) !== 0 || Number(odd.hf_ou.over_od) !== 0 || Number(odd.hf_ou.under_od) !== 0 || Number(odd.hf_1x2.home_od) || Number(odd.hf_1x2.away_od) !== 0 || Number(odd.hf_1x2.draw_od) !== 0;
      return <> {isShow && <>
        <div className="bettype_group" data-more-lines="true">
          {match.odds.length > 1 && <div className="btn btn-more-lines" data-full-width="false" onClick={() => setMoreLines(!moreLines)}><span className="btn_text-line-2">Cược Châu Á khác</span></div>}
          <div className="bettype_item">
            <div className="bettype_name">Hiêp 1 - Cược Chấp</div>
            {Number(odd.hf_hdp.home_od) !== 0 && <div className={`bettype_oddsbox ${home ? `comodds-${home}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_hdp.home_od') ? 'is-active' : ''} onClick={() => openBet('hf_hdp.home_od', odd.odd_id)}>
              <div className="oddsbox_goal">{odd?.hf_hdp.handicap_team === 'home' && odd.hf_hdp.handicap}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_hdp.home_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_hdp.home_od)).toFixed(2)}</div>
            </div>}
            {Number(odd.hf_hdp.away_od) !== 0 && <div className={`bettype_oddsbox ${away ? `comodds-${away}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_hdp.away_od') ? 'is-active' : ''} onClick={() => openBet('hf_hdp.away_od', odd.odd_id)}>
              <div className="oddsbox_goal">{odd.hf_hdp.handicap_team === 'away' && odd.hf_hdp.handicap}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_hdp.away_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_hdp.away_od)).toFixed(2)}</div>
            </div>
            }
            <div className="bettype_oddsbox"></div>
          </div>
          <div className="bettype_item">
            <div className="bettype_name">Hiêp 1 - Tài/Xỉu</div>
            {Number(odd.hf_ou.over_od) !== 0 && <div className={`bettype_oddsbox ${o ? `comodds-${o}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_ou.over_od') ? 'is-active' : ''} onClick={() => openBet('hf_ou.over_od', odd.odd_id)}>
              <div className="oddsbox_goal">o {odd.hf_ou.handicap}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_ou.over_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_ou.over_od)).toFixed(2)}</div>
            </div>}
            {Number(odd.hf_ou.under_od) !== 0 && <div className={`bettype_oddsbox ${u ? `comodds-${u}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_ou.under_od') ? 'is-active' : ''} onClick={() => openBet('hf_ou.under_od', odd.odd_id)}>
              <div className="oddsbox_goal">{odd.hf_ou.under_od ? 'u' : ''}</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_ou.under_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_ou.under_od)).toFixed(2)}</div>
            </div>}
            <div className="bettype_oddsbox"></div>
          </div>
          <div className="bettype_item">
            <div className="bettype_name">Hiêp 1 - 1X2</div>
            {Number(odd.hf_1x2.home_od) !== 0 && <div className={`bettype_oddsbox ${xh ? `comodds-${xh}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_1x2.home_od') ? 'is-active' : ''} onClick={() => openBet('hf_1x2.home_od', odd.odd_id)}>
              <div className="oddsbox_goal">Đội nhà</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_1x2.home_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_1x2.home_od)).toFixed(2)}</div>
            </div>}
            {Number(odd.hf_1x2.away_od) !== 0 && <div className={`bettype_oddsbox ${xa ? `comodds-${xa}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_1x2.away_od') ? 'is-active' : ''} onClick={() => openBet('hf_1x2.away_od', odd.odd_id)}>
              <div className="oddsbox_goal">Đội khách</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_1x2.away_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_1x2.away_od)).toFixed(2)}</div>
            </div>}
            {Number(odd.hf_1x2.draw_od) !== 0 && <div className={`bettype_oddsbox ${dr ? `comodds-${dr}` : ''}`} data-status={(selecteParlay && selecteParlay.betType === 'hf_1x2.draw_od') ? 'is-active' : ''} onClick={() => openBet('hf_1x2.draw_od', odd.odd_id)}>
              <div className="oddsbox_goal">Hòa</div>
              <div className={`oddsbox_odds ${convertMalayToDecimal(odd.hf_1x2.draw_od) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(odd.hf_1x2.draw_od)).toFixed(2)}</div>
            </div>
            }
          </div>
        </div>
      </>}
      </>
    }
  }
  const headers = () => {
    return <div className="commatch_header">
      <div className="commatch_stat" style={{ background: 'transparent'}}>
        {tab === 'live' && <div className="commatch_score">{match.ss}</div>}
        <div className="commatch_time">
          <div className="slideshow" data-ani="">
            <div className="slideshow_1">
              <div>{match.time}</div>
            </div>
            <div className="slideshow_2"></div>
          </div>
        </div>
      </div>
      <div className="commatch_team">
        <div className="commatch_home"><span>{match.home}</span>{Number(match.reds.home) > 0 && <div className="match_redcard">{match.reds.home}</div>}</div>
        <div className="commatch_versus">vs</div>
        <div className="commatch_away"><span>{match.away}</span>
          {Number(match.reds.away) > 0 && <div className="match_redcard">{match.reds.away}</div>}
        </div>
      </div>
      <div className="commatch_arrow">
        <div className="btn" onClick={() => setCommatch(!commatch)}><i className="icon icon-arrow-bottom"></i></div>
      </div>
    </div>
  }
  const mathInfo = () => {
    return <div className="commatch_info">
      <div className="commatch_stat">
        {tab === 'live' && <div className="commatch_score">{match.ss}</div>}
        {tab === 'today' && <div className="commatch_score">{match.timer?.live === 'Live' ? 'Trực tiếp' : ''}</div>}
        <div className="commatch_time">
          <div className="slideshow" data-ani="">
            <div className="slideshow_1" style={{ fontSize: '12px' }}>
              {tab === 'live' && <div>{match.time}</div>}
              {tab === 'today' && <div style={{ color: 'red' }}>{match.timer?.date} - {match.timer?.time.split('</font>')[1]}</div>}
            </div>
            <div className="slideshow_2"></div>
          </div>
        </div>
      </div>
      <div className="commatch_home"><span>{match.home}</span>
        {Number(match.reds.home) > 0 && <div className="match_redcard">{match.reds.home}</div>}
      </div>
      <div className="commatch_away"><span>{match.away}</span>
        {Number(match.reds.away) > 0 && <div className="match_redcard">{match.reds.away}</div>}</div>
      <div className="commatch_stream"><a className="c-btn c-btn--myfav" data-added="false"><i
        className="c-icon c-icon--star-outline"></i></a>
        <div className="commatch_stream-icon-group">
          <i className="icon icon-tv" onClick={() => navigate('/livematch/' + match.event_id)}></i>
          {tab === 'live' && <i className="icon icon-gv" onClick={() => navigate('/statistics/' + match.event_id)}></i>}
        </div>
        {(match.live_id !== "" && tab === 'live') && <div className="btn btn-mini-streaming" data-open={openStream ? 'true' : 'false'} onClick={() => setOpenStream(!openStream)} style={{ textAlign: 'center' }}>
          <i className="icon icon-mini-streaming"></i>
        </div>}
      </div>
    </div>
  }
  const cellMore = (odd: any) => {
    if (odd) {
      const selecteParlay: any = parlayData && parlayData.find((p: any) => p.oddId === odd.odd_id);
      let goalHome: any;
      let valueHome = 0;
      let goalAway: any;
      let valueAway = 0;

      let goalDraw: any;
      let valueDraw = 0;

      let betTypeHome: any;
      let betTypeAway: any;
      let betTypeDraw: any;
      if (tabMoreLive === 'ft_hdp' && (odd?.ft_hdp.home_od !== undefined && odd?.ft_hdp.home_od !== '')) {
        if (odd.ft_hdp.handicap_team === 'home') { goalHome = odd.ft_hdp.handicap }
        if (odd.ft_hdp.handicap_team === 'away') { goalAway = odd.ft_hdp.handicap }

        valueHome = odd.ft_hdp.home_od;
        valueAway = odd.ft_hdp.away_od;
        betTypeHome = 'ft_hdp.home_od'
        betTypeAway = 'ft_hdp.away_od'
      }

      if (tabMoreLive === 'ft_ou' && (odd?.ft_ou.under_od !== undefined && odd?.ft_ou.under_od !== '')) {
        goalHome = 'u'
        goalAway = '0'

        valueHome = odd.ft_ou.over_od;
        valueAway = odd.ft_ou.under_od;
        betTypeHome = 'ft_ou.over_od'
        betTypeAway = 'ft_ou.under_od'
      }

      if (tabMoreLive === 'ft_1x2' && (odd?.ft_1x2.home_od !== undefined && odd?.ft_1x2.home_od !== '')) {
        goalHome = 'Đội nhà'
        goalAway = 'Đội khách'
        goalDraw = 'Hòa'

        valueHome = odd.ft_1x2.home_od;
        valueAway = odd.ft_1x2.away_od;
        valueDraw = odd.ft_1x2.draw_od;

        betTypeHome = 'ft_1x2.home_od'
        betTypeAway = 'ft_1x2.away_od'
        betTypeDraw = 'ft_1x2.draw'
      }

      if (tabMoreLive === 'hf_hdp' && (odd?.hf_hdp.home_od !== undefined && odd?.hf_hdp.home_od !== '')) {

        if (odd.hf_hdp.handicap_team === 'home') { goalHome = odd.hf_hdp.handicap }
        if (odd.hf_hdp.handicap_team === 'away') { goalAway = odd.hf_hdp.handicap }

        valueHome = odd.hf_hdp.home_od;
        valueAway = odd.hf_hdp.away_od;
        betTypeHome = 'hf_hdp.home_od'
        betTypeAway = 'hf_hdp.away_od'
      }

      if (tabMoreLive === 'hf_ou' && (odd?.hf_ou.under_od !== undefined && odd?.hf_ou.under_od !== '')) {
        goalHome = 'u'
        goalAway = '0'

        valueHome = odd.hf_ou.over_od;
        valueAway = odd.hf_ou.under_od;
        betTypeHome = 'hf_ou.over_od'
        betTypeAway = 'hf_ou.under_od'
      }

      if (tabMoreLive === 'hf_1x2' && odd?.hf_1x2.home_od !== undefined && odd?.hf_1x2.home_od !== '') {
        goalHome = 'Đội nhà'
        goalAway = 'Đội khách'
        goalDraw = 'Hòa'

        valueHome = odd.hf_1x2.home_od;
        valueAway = odd.hf_1x2.away_od;
        valueDraw = odd.hf_1x2.draw_od;

        betTypeHome = 'hf_1x2.home_od'
        betTypeAway = 'hf_1x2.away_od'
        betTypeDraw = 'hf_1x2.draw'
      }


      return <>
        {valueHome !== 0 && valueAway !== 0 && <div className="bettype_item">
          {valueHome !== 0 && <div className="bettype_oddsbox" data-status={(selecteParlay && selecteParlay.betType === betTypeHome) ? 'is-active' : ''} onClick={() => openBet(betTypeHome, odd.odd_id)}>
            <div className="oddsbox_goal">{goalHome}</div>

            <div className={`oddsbox_odds ${convertMalayToDecimal(valueHome) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(valueHome)).toFixed(2)}</div>
          </div>}
          {valueAway !== 0 && <div className="bettype_oddsbox" data-status={(selecteParlay && selecteParlay.betType === betTypeAway) ? 'is-active' : ''} onClick={() => openBet(betTypeAway, odd.odd_id)}>
            <div className="oddsbox_goal">{goalAway}</div>
            <div className={`oddsbox_odds ${convertMalayToDecimal(valueAway) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(valueAway)).toFixed(2)}</div>
          </div>}

          {valueDraw !== 0 && <div className="bettype_oddsbox" data-status={(selecteParlay && selecteParlay.betType === betTypeDraw) ? 'is-active' : ''} onClick={() => openBet(betTypeDraw, odd.odd_id)}>
            <div className="oddsbox_goal">{goalDraw}</div>
            <div className={`oddsbox_odds ${convertMalayToDecimal(valueDraw) < 0 ? 'betType-minusOdds' : ''}`}>{Number(convertMalayToDecimal(valueDraw)).toFixed(2)}</div>
          </div>}
        </div>
        }
      </>
    }
  }

  const MoreLines = () => {

    let letShowFtHdp = false;
    let letShowFt1X2 = false;
    let letShowFtOu = false;
    let letShowHfHdp = false;
    let letShowHf1X2 = false;
    let letShowHfOu = false;
    const odd = match.odds[1];

    if (odd.ft_hdp.home_od !== '') { letShowFtHdp = true; }
    if (odd.ft_1x2.home_od !== '') { letShowFt1X2 = true; }
    if (odd.ft_ou.over_od !== '') { letShowFtOu = true; }
    if (odd.hf_hdp.home_od !== '') { letShowHfHdp = true; }
    if (odd.hf_1x2.home_od !== '') { letShowHf1X2 = true; }
    if (odd.hf_ou.over_od !== '') { letShowHfOu = true; }
    
    
    return <div className="c-more-lines" data-open={moreLines ? 'true' : 'false'}>
      <div className="c-more-lines__content">
        <div className="commatch_info">
          <div className="commatch_stat">
            {tab === 'live' && <div className="commatch_score">{match.ss}</div>}
            {tab === 'today' && <div className="commatch_score">{match.timer?.live === 'Lives' ? 'Trực tiếp' : ''}</div>}
            <div className="commatch_time">
              <div className="slideshow" data-ani="">
                <div className="slideshow_1" style={{ fontSize: '12px' }}>
                  {tab === 'live' && <div>{match.time}</div>}
                  {tab === 'today' && <div style={{ color: 'red' }}>{match.timer?.date} - {match.timer?.time.split('</font>')[1]}</div>}
                </div>
                <div className="slideshow_2"></div>
              </div>
            </div>
          </div>
          <div className="commatch_home"><span>{match.home}</span>
            {Number(match.reds.home) > 0 && <div className="match_redcard">{match.reds.home}</div>}
          </div>
          <div className="commatch_away"><span>{match.away}</span>
            {Number(match.reds.away) > 0 && <div className="match_redcard">{match.reds.away}</div>}</div>
          <div className="commatch_stream"><a className="c-btn c-btn--myfav" data-added="false"><i
            className="c-icon c-icon--star-outline"></i></a>
            <div className="commatch_stream-icon-group">
              <i className="icon icon-tv" onClick={() => navigate('/livematch/' + match.event_id)}></i>
              {tab === 'live' && <i className="icon icon-gv" onClick={() => navigate('/statistics/' + match.event_id)}></i>}
            </div>
            {(match.live_id !== "" && tab === 'live') && <div className="btn btn-mini-streaming" data-open={openStream ? 'true' : 'false'} onClick={() => setOpenStream(!openStream)} style={{ textAlign: 'center' }}>
              <i className="icon icon-mini-streaming"></i>
            </div>}
          </div>
        </div>
        <div className="commatch_bettype">
          <div className="c-tabs">
            <div className="c-tabs__content">
              {letShowFtHdp && <div className="c-tab" data-selected={tabMoreLive === 'ft_hdp' ? 'true' : 'false'} onClick={() => setTabMoreLive('ft_hdp')}>
                <div className="btn">Toàn Trận - Cược Chấp</div>
              </div>}
              {letShowFtOu && <div className="c-tab" data-selected={tabMoreLive === 'ft_ou' ? 'true' : 'false'} onClick={() => setTabMoreLive('ft_ou')}>
                <div className="btn">Toàn Trận - Tài/Xỉu</div>
              </div>}
              {letShowFt1X2 && <div className="c-tab" data-selected={tabMoreLive === 'ft_1x2' ? 'true' : 'false'} onClick={() => setTabMoreLive('ft_1x2')}>
                <div className="btn">Toàn Trận - 1X2</div>
              </div>}
              {letShowHfHdp && <div className="c-tab" data-selected={tabMoreLive === 'hf_hdp' ? 'true' : 'false'} onClick={() => setTabMoreLive('hf_hdp')}>
                <div className="btn">Hiệp 1 - Cược Chấp</div>
              </div>}
              {letShowHfOu && <div className="c-tab" data-selected={tabMoreLive === 'hf_ou' ? 'true' : 'false'} onClick={() => setTabMoreLive('hf_ou')}>
                <div className="btn">Hiệp 1 - Tài/Xỉu</div>
              </div>}
              {letShowHf1X2 && <div className="c-tab" data-selected={tabMoreLive === 'hf_1x2' ? 'true' : 'false'} onClick={() => setTabMoreLive('hf_1x2')}>
                <div className="btn">Hiệp 1 - 1X2</div>
              </div>}
            </div>
          </div>
          <div className="bettype_group" data-selected="true">

            {
              oddMores.map((odd, idx) => {
                return <div key={idx}>{cellMore(odd)}</div>
              })
            }
          </div>
          <div className="c-more-lines__btn" onClick={() => setMoreLines(!moreLines)}>
            <div className="btn_text">Đóng</div><i className="icon icon-clear"></i>
          </div>
        </div>
      </div>
    </div >
  }
  const streaming = () => {

    if (openStream && match.live_id !== "") {
      return <div className="c-mini-streaming__content">
        <div className="c-mini-streaming__video">
          <div className="c-mini-streaming__view">
            {/* <div className="c-gc-msg c-gc-msg--overlay" data-open="false"><i className="c-gc-icon c-gc-icon--wrench-filled"></i>
              <div className="c-gc-msg__text">
                <p>Tính năng phát trực tiếp đang được bảo trì và sẽ sớm hoạt động trở lại.</p>
              </div>
            </div> */}
            <div id="GVFrame">
              {!loading && <iframe id="iframePlayer" src={srcStream} height="232px" width="414px" style={{ margin: '0px' }} scrolling="no"></iframe>}

              <div className="video_loading" style={{ display: loading ? 'flex' : 'none' }}>
                <div className="preloader ">
                  <div className="preloader_spiner">
                    <div className="preloader_circles">
                      <div className="preloader_circle-1"></div>
                      <div className="preloader_circle-2"></div>
                    </div>
                  </div>
                </div>
                <div className="text-loading">Đang tải phát trực tiếp...</div>
                <div className="text-directions">Lưu ý: Chất lượng phát trực tuyến phụ thuộc vào mạng di động của bạn.</div>
              </div>
            </div>
          </div>
        </div>
        <div className="c-mini-streaming__info">
          <div className="c-mini-streaming__stat">
            <div className="c-mini-streaming__score">{match.ss}</div>
            <div className="c-mini-streaming__time">
              <div className="slideshow" data-ani="">
                <div className="slideshow_1">
                  <div>{match.time}</div>
                </div>
                <div className="slideshow_2"></div>
              </div>
            </div>
            <div className="c-mini-streaming__btn-group">
              <div className="btn btn-full-screen-on" onClick={() => setfullScreen(!fullScreen)}><i className="icon icon-full-screen-on"></i></div>
              <div className="btn btn-full-screen-off" onClick={() => setfullScreen(!fullScreen)}><i className="icon icon-full-screen-off"></i></div>
              <div className="btn btn-return"><i className="icon icon-return"></i></div>
              <div className="btn btn-clear" onClick={() => setOpenStream(!openStream)}><i className="icon icon-clear"></i></div>
            </div>
          </div>
          <div className="c-mini-streaming__team">
            <div className="c-mini-streaming__home"><span className="text">{match.home}</span></div>
            <div className="c-mini-streaming__verse">-vs-</div>
            <div className="c-mini-streaming__away"><span className="text">{match.away}</span></div>
          </div>
        </div>
      </div>
    }
  }
  const paneDidMount = (node: any) => {
    if (node) {
      node.addEventListener("scroll", handleScroll.bind(this));
    }
  }

  const openBet = (bet: string, odd: any) => {
    
    const _bet = {
      leagueId: match.league_id,
      betType: bet,
      oddId: odd,
      eventId: match.event_id,
      gameType: gameType
    }

    if (parlay) {
      let prPl = parlayData.findIndex((p: any) => p.eventId === match.event_id);
      if (prPl > -1) {
        if (parlayData[prPl].betType === bet && parlayData[prPl].oddId === odd) {
          //Remove
          setParlayData(parlayData.filter((p: any) => p.eventId !== match.event_id));
        } else {
          //update
          parlayData[prPl] = { ...parlayData[prPl], betType: bet, oddId: odd }
          setParlayData([...parlayData]);
        }

      } else {
        setParlayData([...parlayData, _bet]);
      }
    } else {
      props.setBet(_bet);
    }


  }
  const handleScroll = (event: any) => {
    var node = event.target;
    if (node.scrollLeft > (node.clientWidth / 2)) {
      setActive(false);
    } else if (node.scrollLeft < (node.clientWidth / 2)) {
      setActive(true);
    }
    // const bottom = node.scrollHeight - node.scrollTop === node.clientHeight;
  }
  const pagination = () => {
    const odd = match.odds[0];
    const isShow = Number(odd?.hf_hdp.home_od) !== 0 || Number(odd?.hf_hdp.away_od) !== 0 || Number(odd?.hf_ou.over_od) !== 0 || Number(odd?.hf_ou.under_od) !== 0 || Number(odd?.hf_1x2.home_od) || Number(odd?.hf_1x2.away_od) !== 0 || Number(odd?.hf_1x2.draw_od) !== 0;
    // data-status="is-active"
    return <div className="bettype_pagination">
      <ul>
        <li data-status={active ? 'is-active' : ''}></li>
        {isShow && <li data-status={!active ? 'is-active' : ''}></li>}
      </ul>
    </div>
  }

  return <>
    <div className="commatch" data-status={commatch ? 'is-open' : ''} data-mini-streaming="true" data-scores="false">
      {match.live_id !== "" && <div className="c-mini-streaming" data-open={openStream ? 'true' : 'false'} data-full-screen={fullScreen ? 'true' : 'false'}>{streaming()}</div>}
      {headers()}
      <div className="commatch_content" style={{ display: commatch ? 'flex' : 'none' }}>
        {mathInfo()}
        <div className="commatch_bettype">
          <div className="bettype_list" ref={paneDidMount}>
            {ftHdp()}
            {hf1x2()}

          </div>
          {pagination()}
        </div>
        <div className="commatch_btn">
          <div className="commatch_arrow">
            <div className="btn" onClick={() => setCommatch(!commatch)}><i className="icon icon-arrow-bottom"></i></div>
          </div>
          <div className="commatch_more">
            <div className="btn btn-highlight">22+</div>
          </div>
        </div>
        {match.odds.length > 1 && MoreLines()}
      </div>
    </div >
  </>
}