import { useEffect, useState } from "react";
import SVG from "react-inlinesvg";

import iconSuccess from '../../Assets/images/success.svg';
import iconReject from '../../Assets/images/reject.svg';
import { inPlayById } from "./sportCrud";
export const ConFirmBet = (props: any) => {
  const { process, setAppBarSelect, setOpenConfirm } = props;
  const [status, setStatus] = useState('');
  const updateData = () => {
    setTimeout(() => {
      inPlayById(props.betSuccess.id).then(({ data }) => {
        if (data.data.status === 'pending') {
          updateData();
        } else if (data.data.status === 'runing') {
          setStatus('accept');
        } else if (data.data.status === 'cancel') {
          setStatus('reject');
        }

      })
    }, 3000)
  }
  useEffect(() => {
    if (props.betSuccess) {
      updateData()
    }
    setStatus(process)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.betSuccess])
  return <div className="comquickBet_container" data-status="is-open" style={{ zIndex: '111' }}>
    <div className="comalert">
      <div className="comalert_text"></div>
    </div><i className="icon icon-clear" onClick={() => props.setBet(null)}></i>
    <div>
      <div className="comtickets ">
        <div className="comtickets_head"><i className="icon icon-sport1"></i>
          <div className="comtickets_league">{props.event.league_name}</div>
          <div className="comtickets_match">
            <div className="comtickets_home">{props.event.home}</div>
            <div className="comtickets_score">{props.event.ss}</div>
            <div className="comtickets_away">{props.event.away}</div><i className="icon icon-cashout"></i>
          </div>
        </div>
        <div className="comtickets_status">
          <div className="comtickets_top" data-scroll="true">
            <div className="comtickets_betType">Bóng đá / {props.betType}</div>
            <div className="comtickets_team">
              <div className="combetType_choice">{props.betChoice}</div>
              <div className="combetType_goal">{props.goal}</div>
            </div>
          </div>
          <div className="comtickets_bottom">
            <div className="comtickets_odds  betType-minusOdds">{props.betValue}</div>
            <div className="comodds_type">(MY)</div>
          </div>
        </div>
      </div>
    </div>
    <div>

      <div className="comtickets_mystake" js-comtickets_mystake="">
        <div className="comtickets_info">
          <div className="comtickets_item">
            <div className="comtickets_title">Tiền cược:</div>
            <div className="comtickets_value">{props.betBalanceFormat.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
          </div>
          <div className="comtickets_item">
            <div className="comtickets_title">Thanh toán:</div>
            <div className="comtickets_value">{props.payAmount.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')}</div>
          </div><span className="c-text-code"></span>
        </div>
      </div>
      <div className="comtickets_process" data-status={status}>
        <div className="comtickets_row">
          <div className="comtickets_icon">
            <div className="preloader preloader-mini">
              <div className="preloader_spiner">
                <div className="preloader_circles">
                  <div className="preloader_circle-1"></div>
                  <div className="preloader_circle-2"></div>
                </div>
              </div>
            </div>
            {status === 'accept' && <div className="iconani-done" data-animate="true">
              <SVG src={iconSuccess} />
            </div>}
            {status === 'reject' && <div className="iconani-warn" data-animate="true">
              <SVG src={iconReject} />
            </div>}
          </div>
          <div className="comtickets_col">
            {status === 'process' && <div className="comtickets_subject" js-subject_process="">Cược đang diễn ra...</div>}
            {status === 'accept' && <><div className="comtickets_subject" js-subject_accept="">Đơn cược đã được chấp nhận.</div><div className="comtickets_directions" js-directions="">ID: {props.betSuccess.id}</div></>}
            {status === 'reject' && <><div className="comtickets_subject" js-subject_accept="">Đơn cược đã bị hủy.</div><div className="comtickets_directions" js-directions="">ID: {props.betSuccess.id}</div></>
            }
          </div>
        </div>
        <div className="combetting">
          <div className="combtn-box" js-btn_process="">
            <div className="lc-btn lc-btn-chat"><i className="lc-icon lc-icon--live-chat"></i></div>
            <div className="combtn btn-highlight" js-btn_otherbet="" style={{ background: status === 'accept' ? '#5f86c5' : '' }} onClick={() => props.setBet(null)}>Đặt cược vào các sự kiện khác.</div>
            <div className="combtn btn-highlight" js-btn_checkbet="" style={{ background: status === 'accept' ? '#5f86c5' : '' }} onClick={() => {props.setBet(null); setAppBarSelect('statement'); setOpenConfirm(false)}}><span className="c-text">Kiểm tra Sao kê</span>
              {status === 'process' && <div className="c-badge-group"><span className="c-badge c-badge--highlight">1</span></div>}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

}